// Body
.body {
  display: flex;
  flex-direction: column;
  margin: 0;
  padding: 0;
  background-color: #999;
  color: $black;
  font-family: $font-family;
  font-size: $font-size;
  font-weight: $regular;
  line-height: $line-height;
}

// Reset
html {
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

img {
  max-width: 100%;
  border: none;
}

.content {
  flex: 1 0 auto;

  &__update {
    position: relative;
    transform-origin: top;
    transition: ease transform 800ms;

    &::before {
      content: '';
      display: block;
      width: 100%;
      height: 100%;
      position: absolute;
      top: 0;
      left: 0;
      z-index: 1;
      opacity: 0;
      visibility: hidden;
      background-color: #fff;
      transition: ease opacity 300ms, ease visibility 300ms;
    }

    &::after {
      content: '';
      display: block;
      width: 42px;
      height: 42px;
      position: absolute;
      top: 100px;
      left: calc(50% - 21px);
      z-index: 2;
      opacity: 0;
      visibility: hidden;
      background-image: url('/img/spinner.png');
      background-repeat: no-repeat;
      background-position: 50% 50%;
      background-size: contain;
      animation: rotate360 1s ease infinite;
      transition: ease opacity 400ms, ease visibility 400ms;
    }

    &.is-loading {
      &::before {
        opacity: 1;
        visibility: visible;
      }

      &::after {
        // opacity: 0.5;
        // visibility: visible;
      }
    }
  }
}

// Titles
h1 {
  @include title();
  font-size: $font-size--h1;
}

h2 {
  @include title();
  font-size: $font-size--h2;
}

h3 {
  @include title();
  font-size: $font-size--h3;
}

h4 {
  @include title();
  font-size: $font-size--h4;
}

h5 {
  @include title();
  font-size: $font-size--h5;
}

h6 {
  @include title();
  font-size: $font-size--h6;
}

// Focus
a:focus,
button:focus {
  outline: none;
}

input:focus,
textarea:focus,
select:focus {
  outline: 2px dashed #86d3ff;
}

.is-focused {
  outline: 2px dashed #86d3ff !important;
}

// Hide/Hide
.is-hide {
  display: none;
}

@media (max-width: $xl) {
  .is-xl-hide {
    display: none;
  }
}

@media (max-width: $l) {
  .is-l-hide {
    display: none;
  }
}

@media (max-width: $md) {
  .is-md-hide {
    display: none;
  }
}

@media (max-width: $m) {
  .is-m-hide {
    display: none;
  }
}

@media (max-width: $s) {
  .is-s-hide {
    display: none;
  }
}

.is-show {
  display: block;
}

// Aligns
.is-left {
  text-align: left !important;
}
.is-center {
  text-align: center !important;
}
.is-right {
  text-align: right !important;
}
