// Colors
$accent: #243266;
$accent-second: #f7a832;
$black: #333;
$grey: #f2f2f2;
$white: #ffffff;

// Typo
$font-size: 15px;
$font-size--h1: 32px;
$font-size--h2: 24px;
$font-size--h3: 18px;
$font-size--h4: 18px;
$font-size--h5: 18px;
$font-size--h6: 16px;

$font-family: 'Montserrat', sans-serif;
$line-height: 1.6;
$light: 300;
$regular: 400;
$medium: 600;
$bold: 700;

// Breakpoints
$xl: 1367px;
$l: 1181px;
$md: 769px;
$m: 680px;
$s: 561px;
$xs: 321px;

// Grid properties
$gap: 30;
$width: 100%;
$minwidth: 320px;
$maxwidth: 1920px;

$width-col-1p: 100% / 100;
$width-col-5p: $width-col-1p * 5;
$width-col-10p: $width-col-1p * 10;
$width-col-20p: $width-col-10p * 2;
$width-col-30p: $width-col-10p * 3;
$width-col-40p: $width-col-10p * 4;
$width-col-50p: $width-col-10p * 5;
$width-col-60p: $width-col-10p * 6;
$width-col-70p: $width-col-10p * 7;
$width-col-80p: $width-col-10p * 8;
$width-col-90p: $width-col-10p * 9;

$width-col-1: 100% / 12;
$width-col-2: $width-col-1 * 2;
$width-col-3: $width-col-2 + $width-col-1;
$width-col-4: $width-col-2 * 2;
$width-col-5: $width-col-4 + $width-col-1;
$width-col-6: $width-col-2 * 3;
$width-col-7: $width-col-6 + $width-col-1;
$width-col-8: $width-col-2 * 4;
$width-col-9: $width-col-3 * 3;
$width-col-10: $width-col-2 * 5;
$width-col-11: $width-col-10 + $width-col-1;
$width-col-12: $width-col-2 * 6;

// Layers
$layer-1: 100;
$layer-2: 200;
$layer-3: 300;
$layer-4: 400;
$layer-5: 500;

// Other
$gradient: linear-gradient(70deg, #faf2d9 1%, #e8fffe 99%);
$gradient2: linear-gradient(90deg, #ffefc4 0%, #c7ffe7 100%);
$gradient2-reverse: linear-gradient(-90deg, #ffefc4 0%, #c7ffe7 100%);
$border-color: $grey;
$border-radius: 20px;
$opacity: 0.7;
$shadow: 0 8px 40px rgba(0, 51, 51, 0.25);
