/**
* breadcrumb
*/

.breadcrumb {
  $breadcrumb: &;

  margin: 0;
  padding: 0;
  line-height: 1;

  &__item {
    display: inline-block;
    margin-right: 4px;
    padding-right: 12px;
    position: relative;
    color: #999999;
    font-weight: 500;
    font-size: 13px;
    line-height: 1;

    &::after {
      content: '/';
      display: inline-block;
      position: absolute;
      top: calc(50% - 6px);
      right: 0;
      line-height: 1;
    }

    &:last-child {
      margin-right: 0;
      padding-right: 0;

      &::after {
        display: none;
      }
    }
  }

  &__link {
    display: inline-block;
    color: currentColor;
    line-height: 1;
    text-decoration: none;
    transition: ease color 300ms;

    &:hover,
    &:focus {
      color: $accent;
    }

    &:active {
      color: $accent - 20;
    }
  }

  @media (max-width: $s) {
    &__item {
      font-size: 11px;
    }
  }
}

/**
* button
*/

.button {
  $button: &;
  $margin: 0;
  $padding: 22px 32px;
  $font-size: 16px;
  $color: #3a3e42;
  $background-color: #f7a832;
  $border-radius: 3px;
  $border-width: 0;
  $border-color: $white;

  display: inline-block;
  min-width: 180px;
  margin: $margin;
  padding: $padding;
  z-index: 0;
  color: $color;
  border-radius: $border-radius;
  background-color: $background-color;
  border: 0;
  font-size: $font-size;
  font-weight: 700;
  font-family: $font-family;
  vertical-align: top;
  line-height: 1;
  text-decoration: none;
  text-align: center;
  cursor: pointer;
  transition: ease 300ms background, ease 300ms color, ease 300ms opacity;

  &:hover,
  &:focus {
    background-color: #f9bb60;
  }

  &:active {
    background-color: #000000;
    color: #f7a832;
  }

  &[disabled] {
    opacity: 0.5 !important;
    pointer-events: none;
  }

  &.is-blue {
    min-width: 154px;
    color: #fff;
    background-color: $accent;

    &:hover,
    &:focus {
      background-color: $accent + 20;
    }

    &:active {
      background-color: #000000;
      color: #fff;
    }
  }
}

/**
* cart
*/

.cart {
  $cart: &;

  // margin-top: 40px;
  // border-top: 1px solid #c9c9c9;
  transition: ease 300ms opacity;

  .js-init & {
    opacity: 0;
  }

  &.is-ready {
    opacity: 1;
  }

  &__empty {
    padding: 80px;
    color: #999;
    font-size: 14px;
    text-align: center;
    line-height: 1.2;
  }

  &__list {
    padding: 0 80px;
  }

  &__item {
  }

  &__price {
    display: flex;
    justify-content: flex-end;
    padding: 50px 80px;
    font-weight: 700;
    font-size: 18px;

    &-label {
      color: #333;
    }

    &-value {
      width: 240px;
      margin-left: 70px;
      color: #313f74;
    }
  }

  &__form {
    margin: 70px 0 80px;
    padding: 0 20px;
    text-align: center;
  }

  @media (max-width: 1600px) {
    &__list {
      padding: 0 40px;
    }

    &__price {
      padding: 50px 40px;

      &-value {
        width: 180px;
      }
    }

    &__form {
      margin: 50px 0 70px;
    }
  }

  @media (max-width: $l) {
    &__price {
      padding: 40px 0;

      &-value {
        margin-left: 40px;
        width: 140px;
      }
    }

    &__form {
      margin: 20px 0 50px;
    }
  }

  @media (max-width: $s) {
    &__list {
      padding: 0 20px;
    }

    &__price {
      padding: 20px;
      justify-content: flex-start;

      &-value {
        margin-left: 20px;
        width: auto;
      }
    }

    &__form {
      margin-top: 25px;
    }
  }
}

/**
* cart-form
*/

.cart-form {
  $cart-form: &;

  display: inline-block;
  width: 100%;
  max-width: 500px;
}

/**
* cart-item
*/

.cart-item {
  $cart-item: &;

  display: flex;
  justify-content: space-between;
  align-items: center;
  position: relative;
  padding: 60px 0;
  border-bottom: 1px solid #c9c9c9;

  &__cover {
    max-width: 217px;
    max-height: 163px;
    overflow: hidden;
  }

  &__image {
    width: 100%;
    height: 100%;
    object-fit: cover;
  }

  &__title {
    max-width: 35%;
    flex-grow: 1;
    margin: 0 50px;
  }

  &__link {
    color: #333;
    font-weight: 400;
    font-size: 15px;
    text-decoration: none;
    transition: ease color 300ms;

    &:hover,
    &:focus {
      color: $accent;
    }

    &:active {
      color: $accent - 20;
    }
  }

  &__counter {
  }

  &__price {
    width: 240px;
    margin-left: 70px;
    color: #313f74;
    font-weight: 700;
    font-size: 18px;
  }

  &__remove {
    width: 24px;
    height: 24px;
    margin-left: 70px;
    padding: 0;
    position: absolute;
    right: 0;
    top: calc(50% - 12px);
    color: #c4c4c4;
    background-color: transparent;
    border: none;
    cursor: pointer;
    transition: ease color 300ms;

    svg {
      width: 100%;
      height: 100%;
      color: currentColor;
    }

    &:hover,
    &:focus {
      color: $accent;
    }

    &:active {
      color: $accent - 20;
    }
  }

  &.is-mini {
    flex-flow: row wrap;
    align-items: center;
    padding: 20px 0;
    border-bottom: none;

    #{$cart-item}__title {
      width: 100%;
      max-width: none;
      margin: 0;
      margin-bottom: 15px;
      line-height: 1.1;
      order: inherit;
    }

    #{$cart-item}__price {
      width: 100%;
      margin: 0;
      margin-top: 15px;
      line-height: 1;
      order: inherit;
    }

    #{$cart-item}__cover {
      position: static;
      max-width: 96px;
      max-height: 72px;
      order: inherit;
    }

    #{$cart-item}__counter {
      flex: auto;
      margin: 0;
      margin-left: 25px;
      order: inherit;
    }

    #{$cart-item}__remove {
      margin: 0;
      position: relative;
      left: 0;
      top: 0;
      order: inherit;
    }
  }

  @media (max-width: 1600px) {
    padding: 35px 0;

    &__title {
      max-width: 30%;
      margin: 0 30px;
    }
    &__price {
      width: 180px;
    }
  }

  @media (max-width: $l) {
    flex-flow: row wrap;
    align-items: flex-start;
    min-height: 165px;
    padding: 30px 0;
    padding-left: 160px;

    &__cover {
      width: 140px;
      height: 105px;
      position: absolute;
      top: 30px;
      left: 0;
    }

    &__title {
      width: 100%;
      flex-grow: 1;
      max-width: none;
      margin: 0 0 15px;
    }

    &__price {
      width: 140px;
      margin-left: 0;
    }
  }

  @media (max-width: $s) {
    align-items: center;
    padding: 25px 0;

    &__cover {
      order: 1;
      width: 96px;
      height: 72px;
      position: relative;
      top: 0;
    }

    &__title {
      order: 0;
      width: 100%;
      flex-grow: 1;
      max-width: none;
      margin: 0 0 15px;
    }

    &__counter {
      order: 2;
      flex: 1;
      margin: 0 25px;
    }

    &__price {
      order: 3;
      width: 100%;
      margin: 15px 0 0;
    }
  }
}

/**
* cat
*/

.cat {
  $cat: &;

  display: block;
  text-decoration: none;
  text-align: center;

  &.is-card {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-flow: column nowrap;
    height: 720px;
    margin: auto;
    padding: 60px 70px;
    padding: 60px 8%;
    position: relative;
    z-index: 0;
    background-color: #fff;
    overflow: hidden;

    #{$cat}__title {
      min-height: 80px;
      margin: 35px 0;
      color: #333;
      font-weight: 700;
      font-style: normal;
      font-size: 32px;
      line-height: 80px;
      text-align: center;

      i {
        display: inline-block;
        line-height: 1.2;
        font-style: normal;
        vertical-align: middle;
      }
    }
  }

  &.is-alt {
    #{$cat}__cover {
      display: block;
      width: calc(100% + 100px);
      height: 100%;
      max-width: none;
      position: absolute;
      left: 0;
      top: 0;
      background-repeat: no-repeat;
      background-position: 50% 50%;
      background-size: cover;
      transition: ease 5s transform;
      transform: translateX(-100px);
    }

    #{$cat}__label,
    #{$cat}__title {
      color: #fff;
    }

    #{$cat}__arrow {
      background-image: url('/img/cat-arrow-alt.svg');
    }
  }

  &__object {
    position: absolute;
    transition: ease-in-out 600ms transform;
    z-index: 0;

    &::after {
      content: '';
      display: block;
      width: 100%;
      height: 100%;
      position: absolute;
      top: 0;
      left: 0;
      background-repeat: no-repeat;
      background-position: 50% 50%;
      background-size: contain;
      animation: twist 20s ease-in-out infinite both;
      animation-direction: alternate;
    }

    &:nth-child(even)::after {
      animation-direction: alternate-reverse;
    }

    &.is-item1 {
      top: calc(50% - 325px);
      left: calc(50% - 370px);
      width: 178px;
      height: 307px;

      &::after {
        background-image: url(/img/obj1.png);
        animation-delay: -100ms;
        transform: rotate(22deg);
      }
    }

    &.is-item2 {
      width: 204px;
      height: 173px;
      top: calc(50% - 270px);
      right: calc(50% - 330px);

      &::after {
        animation-delay: -200ms;
        background-image: url(/img/obj2.png);
        transform: rotate(20deg);
      }
    }

    &.is-item3 {
      width: 270px;
      height: 164px;
      top: calc(50% + 50px);
      right: calc(50% - 425px);

      &::after {
        background-image: url(/img/obj3.png);
        transform: rotate(-15deg);
      }
    }

    &.is-item4 {
      width: 198px;
      height: 179px;
      top: calc(50% + 50px);
      left: calc(50% - 380px);

      &::after {
        background-image: url(/img/obj4.png);
        animation-delay: -250ms;
        transform: rotate(-15deg);
      }
    }
  }

  &__cover {
    display: flex;
    justify-content: center;
    align-items: flex-end;
    width: 100%;
    height: 236px;
    // max-width: 200px;
  }

  &__image {
    color: #fff;
    pointer-events: none;
    transition: ease color 300ms;

    &[data-id='1'] {
      width: 176px;
      height: 234px;
    }

    &[data-id='2'] {
      width: 86px;
      height: 150px;
    }

    &[data-id='3'] {
      width: 132px;
      height: 172px;
    }

    &[data-id='4'] {
      width: 160px;
      height: 234px;
    }

    &[data-id='5'] {
      width: 142px;
      height: 124px;
    }

    &[data-id='6'] {
      width: 124px;
      height: 146px;
    }

    &[data-id='7'] {
      width: 90px;
      height: 128px;
    }

    &[data-id='8'] {
      width: 100px;
      height: 155px;
    }

    &[data-id='9'] {
      width: 140px;
      height: 127px;
    }

    &[data-id='10'] {
      width: 146px;
      height: 100px;
    }

    &[data-id='11'] {
      width: 108px;
      height: 128px;
    }

    &[data-id='12'] {
      width: 200px;
      height: 148px;
    }
  }

  &__content {
    display: flex;
    flex-direction: column;
    flex-grow: 1;
    align-items: center;
    justify-content: center;
    max-width: 340px;
    position: relative;
    z-index: 1;
  }

  &__label {
    display: inline-block;
    color: #cccccc;
    font-weight: 700;
    font-size: 56px;
    line-height: 1;
  }

  &__title {
    margin: 0;
    margin-top: 25px;
    font-weight: 600;
    font-size: 15px;
    line-height: 1;
  }

  &__arrow {
    display: inline-block;
    width: 53px;
    height: 45px;
    margin-top: 20px;
    background-image: url('/img/cat-arrow.svg');
    background-repeat: no-repeat;
    background-position: 50% 50%;
    background-size: contain;
    opacity: 0;
    transition: ease-in-out opacity 600ms;
  }

  &__price {
    margin: 0;
    margin-top: 20px;
    color: #313f74;
    font-weight: 700;
    font-size: 16px;
    line-height: 1;
    opacity: 0;
    transform: translateY(10px);
    transition: ease opacity 300ms, ease transform 300ms;
  }

  &__footer {
    display: flex;
    justify-content: space-between;
    align-items: flex-end;
    width: 100%;
    position: relative;
    z-index: 1;
  }

  &__offer {
    color: #333;
    font-weight: 500;
    font-style: normal;
    font-size: 15px;
    text-align: left;

    i {
      display: block;
      margin-top: 5px;
      color: #243266;
      font-weight: 800;
      font-style: normal;
      font-size: 20px;
      line-height: 1;
    }
  }

  &__dots {
    display: flex;

    span {
      width: 7px;
      height: 7px;
      margin: 0 2px;
      margin-bottom: 5px;
      border-radius: 50%;
      background-color: #f7a832;
      transition: transform ease 200ms;
    }
  }

  &:hover {
    &.is-alt {
      #{$cat}__cover {
        transform: translateX(0);
      }
    }
    #{$cat}__object {
      &::after {
        animation-play-state: paused;
      }

      &.is-item1 {
        transform: translate(40px, 20px);
      }

      &.is-item2 {
        transform: translate(-40px, 20px);
      }

      &.is-item3 {
        transform: translate(-40px, -20px);
      }

      &.is-item4 {
        transform: translate(40px, -20px);
      }
    }
  }

  &:hover,
  &:focus {
    #{$cat}__dots {
      span {
        animation: jump 400ms ease alternate infinite;

        &:nth-child(2) {
          animation-delay: 100ms;
        }

        &:nth-child(3) {
          animation-delay: 200ms;
        }
      }
    }

    #{$cat}__arrow {
      opacity: 1;
    }

    #{$cat}__image {
      color: $accent-second;
    }

    #{$cat}__price {
      opacity: 1;
      transform: translateY(0);
    }
  }

  &:active {
    #{$cat}__image {
      color: $accent-second + 20;
    }
  }

  @media (max-width: 1480px) {
    &__object {
      &.is-item1 {
        width: 135px;
        height: 230px;
        left: calc(50% - 240px);
      }

      &.is-item2 {
        width: 155px;
        height: 135px;
        right: calc(50% - 225px);
      }

      &.is-item3 {
        width: 210px;
        height: 125px;
        right: calc(50% - 300px);
      }

      &.is-item4 {
        width: 150px;
        height: 140px;
        top: calc(50% + 30px);
        left: calc(50% - 265px);
      }
    }
  }

  @media (max-width: $xl) {
    &.is-card {
      height: 560px;

      #{$cat}__title {
        margin: 15px 0;
      }
    }
  }

  @media (max-width: $md) {
    &.is-card {
      height: 374px;
      padding: 30px 8%;
      border-bottom: 1px solid #c4c4c4;

      #{$cat}__label {
        font-size: 36px;
      }

      #{$cat}__title {
        min-height: 60px;
        margin: 0;
        font-size: 20px;
        line-height: 60px;
      }
    }

    &__object {
      transform: none !important;

      &.is-item1 {
        width: 95px;
        height: 160px;
        top: 20px;
        left: calc(50% - 210px);
      }

      &.is-item2 {
        width: 120px;
        height: 95px;
        right: calc(50% - 175px);
        top: 30px;
      }

      &.is-item3 {
        width: 150px;
        height: 85px;
        top: calc(50% + 40px);
        right: calc(50% - 220px);
      }

      &.is-item4 {
        width: 110px;
        height: 100px;
        left: calc(50% - 205px);
      }
    }

    &__offer {
      font-size: 11px;

      i {
        margin-top: 0;
        font-size: 15px;
      }
    }

    &__dots {
      span {
        width: 5px;
        height: 5px;
        margin: 0 1.5px;
        margin-bottom: 5px;
      }
    }
  }

  @media (max-width: $m) {
    &.is-card {
      height: 310px;
      padding: 25px 35px;
      border-bottom: none;

      #{$cat}__title {
        min-height: 40px;
        line-height: 40px;
      }

      #{$cat}__arrow {
        height: 35px;
      }
    }

    &__object {
      &.is-item1 {
        width: 75px;
        height: 130px;
        left: calc(50% - 170px);
      }

      &.is-item2 {
        width: 90px;
        height: 85px;
        top: 30px;
        right: calc(50% - 145px);
      }

      &.is-item3 {
        width: 140px;
        height: 75px;
        bottom: 40px;
        right: calc(50% - 180px);
      }

      &.is-item4 {
        width: 80px;
        height: 70px;
        left: calc(50% - 180px);
      }
    }

    &__price {
      opacity: 1;
      transform: none;
    }

    &:not(.is-card) {
      #{$cat}__cover {
        margin-top: 50px;
      }
    }
  }

  @media (max-width: $s) {
    &__arrow {
      display: none;
    }

    &__offer:first-child {
      display: none;
    }

    &:not(.is-card) {
      #{$cat}__cover {
        height: auto;
      }
    }
  }
}

/**
* cats
*/

.cats {
  $cats: &;

  width: 100%;
  padding: 40px 0;

  &__list {
    display: flex;
    flex-flow: row wrap;
  }

  &__item {
    width: 16.6666%;
    flex: 0 0 16.6666%;
    padding: 0 10px;
  }

  @media (max-width: 1660px) {
    &__item {
      width: 20%;
      flex: 0 0 20%;
    }
  }

  @media (max-width: $xl) {
    padding-left: 0;
    padding-right: 0;
  }

  @media (max-width: $l) {
    &__item {
      width: 33.3333%;
      flex: 0 0 33.3333%;
    }
  }

  @media (max-width: 870px) {
    &__item {
      width: 50%;
      flex: 0 0 50%;
    }
  }

  @media (max-width: $m) {
    padding: 0;
    padding-bottom: 20px;

    &__item {
      width: 50%;
      flex: 0 0 50%;
    }
  }

  @media (max-width: $s) {
    &__item {
      width: 100%;
      flex: 0 0 100%;
    }
  }
}

/**
* contact
*/

.contact {
  $contact: &;

  &.is-main {
  }

  &.is-card {
    #{$contact}__address {
      margin-top: 10px;
      font-size: 15px;
    }
  }

  &__map {
  }

  &__cards {
    border-top: 1px solid #c9c9c9;
  }

  &__card {
    padding: 30px 80px 50px;

    & + & {
      border-left: 1px solid #c9c9c9;
    }
  }

  &__header {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }

  &__title {
    max-width: 150px;
    margin: 0;
    color: #333;
    font-weight: 700;
    font-size: 20px;
    line-height: 1.2;
  }

  &__number {
    width: 30px;
    height: 30px;
    background-color: #f7a832;
    color: #333;
    font-weight: 700;
    font-style: normal;
    font-size: 20px;
    text-align: center;
  }

  &__body {
    display: flex;
    margin-top: 30px;
  }

  &__cover {
    max-width: 277px;
    max-height: 186px;
    min-width: 180px;
    line-height: 0;
  }

  &__image {
    max-width: 100%;
    max-height: 100%;
  }

  &__info {
    flex: 1;
    padding-left: 40px;
  }

  &__address {
    width: 100%;
    color: #333;
    font-weight: 400;
    font-style: normal;
    font-size: 12px;
    line-height: 1.2;
  }

  &__phone {
    margin-top: 7px;
    color: #243266;
    font-weight: 600;
    font-size: 18px;
    font-style: normal;
    line-height: 1;
    white-space: nowrap;
  }

  &__marker {
    display: block;
    margin-top: 25px;
    color: #333;
    font-style: normal;
    font-size: 15px;

    &-label {
      font-weight: 600;
    }

    &-text {
      font-weight: 400;
      line-height: 1.2;
    }
  }

  &__time {
    display: block;
    margin-top: 25px;
    color: #333;
    font-weight: 400;
    font-style: normal;
    font-size: 13px;
    text-transform: uppercase;
  }

  &__email {
    display: block;
    margin-top: 25px;
    font-weight: 400;
    font-style: normal;
    font-size: 14px;
    color: #246eb4;
    transition: color ease 300ms;

    &:hover,
    &:focus {
      color: $accent-second;
    }

    &:active {
      color: $accent-second - 20;
    }
  }

  @media (max-width: 1600px) {
    &__card {
      padding: 30px 40px 40px;
    }

    &__cover {
      max-width: 180px;
      min-width: 160px;
    }

    &__info {
      padding-left: 30px;
    }
  }

  @media (max-width: $l) {
    &__card {
      & + & {
        border-top: 1px solid #c9c9c9;
        border-left: none;
      }
    }
  }

  @media (max-width: $md) {
    &__card {
      & + & {
        border-top: none;
        border-left: 1px solid #c9c9c9;
      }
    }

    &__title {
      font-size: 16px;
    }

    &__body {
      margin-top: 20px;
    }

    &__cover {
      max-width: 120px;
      min-width: 80px;
    }

    &__info {
      padding-left: 20px;
    }

    &__email,
    &__time,
    &__marker {
      margin-top: 12px;
    }
  }

  @media (max-width: $m) {
    &__card {
      & + & {
        border-top: 1px solid #c9c9c9;
        border-left: none;
      }
    }
  }

  @media (max-width: $s) {
    &__card {
      padding: 20px 20px 30px;
    }

    &__body {
      flex-direction: column;
    }

    &__cover {
      width: 100%;
      max-width: 100%;
    }

    &__info {
      margin-top: 20px;
      padding-left: 0;
    }
  }
}

/**
* counter
*/

.counter {
  $counter: &;

  display: flex;
  justify-content: space-between;
  width: 100px;
  height: 60px;
  position: relative;
  overflow: hidden;
  border: 1px solid #c9c9c9;
  border-radius: 3px;
  background-color: $white;

  &.is-disabled {
    user-select: none;
    opacity: 0.4;
    pointer-events: none;
  }

  &__field {
    width: 40%;
    max-width: 40%;
    height: 100%;
    flex: 1 0 40%;
    padding: 0;
    color: #333;
    border: none;
    font-weight: 700;
    font-size: 16px;
    font-family: $font-family;
    text-align: center;
    border: 1px solid transparent;
    border-left-color: #c9c9c9;
    border-right-color: #c9c9c9;
    outline: none !important;
    transition: border ease 300ms;

    &:focus {
      border: 1px solid $accent-second;
    }
  }

  &__remove,
  &__add {
    flex: 1 0 30%;
    height: 100%;
    padding: 0;
    position: relative;
    color: #999999;
    border: none;
    background-color: #fff;
    font-family: $font-family;
    font-weight: 700;
    font-style: normal;
    font-size: 16px;
    vertical-align: top;
    cursor: pointer;
    outline: none !important;
    transition: color ease 300ms, background ease 300ms;

    &[disabled] {
      opacity: 0.5;
      pointer-events: none;
    }

    &:hover,
    &:focus {
      color: $accent;
      background-color: #eee;
    }

    &:active {
      color: $accent - 20;
      background-color: #fff;
    }
  }
}

/**
* drop
*/

.drop {
  $drop: &;

  min-width: 210px;

  &__list {
    margin: 0;
    padding: 25px;
    position: relative;
    background-color: #f7a832;
    border-radius: 3px;
    list-style: none;

    &::before {
      content: '';
      display: block;
      position: absolute;
      left: 50px;
      bottom: calc(100% - 1px);
      border: 10px solid transparent;
      border-top-color: transparent;
      border-bottom: 8px solid #f7a832;
    }
  }

  &__item {
    line-height: 1;

    & + & {
      margin-top: 12px;
    }
  }

  &__label {
    position: relative;
  }

  &__field {
    width: 100%;
    height: 100%;
    position: absolute;
    opacity: 0;
    z-index: -1;
  }

  &__text,
  &__link {
    display: inline-block;
    width: 100%;
    color: #333;
    font-weight: 600;
    font-size: 13px;
    font-family: $font-family;
    line-height: 1.2;
    text-decoration: none;
    text-align: left;
    cursor: pointer;
    transition: color ease 400ms;

    &:hover,
    &:focus {
      color: $accent;
    }

    &:active {
      color: $accent - 20;
    }
  }

  &__field:focus + &__text,
  &__field.is-current + &__text {
    color: #2e3f83;
    cursor: default;
  }
}

/**
* feature
*/

.feature {
  $feature: &;

  width: 100%;
  height: 100%;
  padding: 30px;
  position: relative;
  background-color: #333;
  color: #fff;
  text-align: center;

  &.is-accent {
    background-color: $accent-second;
    color: #333;

    #{$feature}__title::before {
      background-color: #333;
    }
  }

  &.has-image {
    background-repeat: no-repeat;
    background-position: 50% 50%;
    background-size: cover;

    &::before {
      content: '';
      display: block;
      width: 100%;
      height: 100%;
      position: absolute;
      top: 0;
      left: 0;
      background-color: #000;
      opacity: 0.5;
    }
  }

  &__title {
    height: 85px;
    margin-top: 30px;
    padding-top: 40px;
    position: relative;
    font-weight: 600;
    font-style: normal;
    font-size: 20px;
    line-height: 1.2;
    text-align: center;

    &::before {
      content: '';
      display: inline-block;
      width: 40px;
      height: 10px;
      position: absolute;
      top: 0;
      left: calc(50% - 20px);
      background-color: $accent-second;
    }
  }

  &__caption {
    margin-top: 40px;
    position: relative;
    font-weight: 500;
    font-style: normal;
    font-size: 15px;
  }

  @media (max-width: $xl) {
    padding: 40px;
    padding-bottom: 20px;

    &__title {
      height: 65px;
      margin-top: 0;
      padding-top: 30px;
      font-size: 16px;

      &::before {
        width: 30px;
        height: 8px;
        left: calc(50% - 15px);
      }
    }

    &__caption {
      margin-top: 20px;
      font-size: 13px;
    }
  }

  @media (max-width: $l) {
    padding: 30px 5px 20px;
  }
}

/**
* features
*/

.features {
  $features: &;

  &__item {
  }

  @media (max-width: $s) {
    &__item {
      & + & {
        margin-top: 10px;
      }
    }
  }
}

/**
* filter
*/

.filter {
  $filter: &;

  padding: 20px 80px;

  &__inner {
    display: flex;
    align-items: center;
    flex-flow: row wrap;
    margin: -10px -20px;
  }

  &__group {
    display: flex;
    align-items: center;
    margin: 10px 20px;
    color: #000;
    font-weight: 400;
    font-style: normal;
    font-size: 13px;
  }

  &__label {
    color: #333;
    font-weight: 400;
    font-size: 13px;
    line-height: 1;
  }

  &__item {
    margin-left: 15px;
    position: relative;
  }

  &__toggle {
    display: flex;
    align-items: center;
    padding: 0;
    position: relative;
    top: -1px;
    background-color: transparent;
    border: none;
    color: #333;
    font-weight: 600;
    font-family: $font-family;
    font-size: 13px;
    line-height: 1;
    text-transform: lowercase;
    cursor: pointer;
    transition: ease color 300ms;

    &::after {
      content: '';
      display: inline-block;
      width: 8px;
      height: 8px;
      margin-left: 7px;
      position: relative;
      top: 1px;
      background-image: url('/img/filter-drop.svg');
      background-repeat: no-repeat;
      background-position: 50% 50%;
      background-size: contain;
    }

    &:hover,
    &:focus {
      color: $accent;
    }

    &:active {
      color: $accent - 20;
    }
  }

  &__drop {
    position: absolute;
    top: calc(100% + 17px);
    left: -10px;
    opacity: 0;
    visibility: hidden;
    transform: scale(0.95);
    transition: ease transform 300ms, ease opacity 300ms, ease visibility 300ms;

    &.is-open {
      opacity: 1;
      visibility: visible;
      transform: scale(1);
    }
  }

  @media (max-width: $xl) {
    padding: 20px 40px;
  }
}

/**
* footer
*/

.footer {
  $footer: &;

  display: flex;
  height: 80px;
  justify-content: space-between;
  align-items: center;
  background-color: #ffffff;
  border-top: 1px solid #c9c9c9;
  padding: 30px 75px;
  position: relative;

  &__text {
    color: #333;
    font-weight: 600;
    font-style: normal;
    font-size: 15px;
    line-height: 1.3;

    b {
      font-weight: inherit;
    }

    &.is-dc {
      padding-left: 37px;
      position: relative;

      &::before {
        content: '';
        display: inline-block;
        width: 28px;
        height: 28px;
        position: absolute;
        left: 0;
        top: calc(50% - 14px);
        background-image: url('/img/dc__.svg');
        background-repeat: no-repeat;
        background-position: 50% 50%;
        background-size: contain;
      }

      a {
        margin-left: 5px;
        color: #246eb4;
        text-decoration: none;
        transition: ease color 300ms;

        &:hover,
        &:focus {
          color: $accent-second;
        }
        &:active {
          color: $accent-second + 20;
        }
      }
    }
  }

  @media (max-width: $l) {
    padding: 30px 40px;
  }

  @media (max-width: $m) {
    height: 60px;
    padding: 20px 25px;

    &__text {
      font-size: 13px;

      b {
        display: block;
      }

      &.is-dc {
        text-align: right;

        a {
          display: block;
        }
      }
    }
  }
}

/**
* form
*/

.form {
  $form: &;

  position: relative;
  transition: opacity 400ms ease;

  &.is-loading {
    opacity: 0.4;
  }

  &.is-error {
    #{$form}__message {
      color: $accent;
    }
  }

  &__title {
    margin: 0;
    margin-bottom: 40px;
    padding-bottom: 40px;
    color: #333;
    position: relative;
    font-weight: 600;
    font-style: normal;
    font-size: 24px;
    text-align: center;
    line-height: 40px;
    line-height: 1;

    &::after {
      content: '';
      display: block;
      width: 40px;
      height: 10px;
      position: absolute;
      left: calc(50% - 20px);
      bottom: 0;
      background-color: $accent-second;
    }
  }

  &__group {
    display: block;
    width: 100%;
    margin-top: 40px;
    position: relative;
    text-align: left;
    line-height: 1;

    &.is-required {
      #{$form}__label {
        &::after {
          content: '*';
          padding-left: 4px;
          color: #f7a832;
        }
      }
    }
  }

  &__label {
    display: block;
    margin-bottom: 20px;
    position: relative;
    color: #333;
    font-weight: 400;
    font-size: 15px;
    line-height: 1;
  }

  &__field {
    display: block;
    width: 100%;
    min-height: 70px;
    padding: 23px 35px;
    color: #333;
    border: 1px solid #c9c9c9;
    background-color: #fff;
    font-weight: 600;
    font-size: 18px;
    font-family: $font-family;
    line-height: 1;
    outline: none !important;
    transition: border 400ms ease;

    &:focus {
      border-color: $accent-second;
    }

    &::-moz-placeholder {
      color: #333;
    }

    &::-webkit-input-placeholder {
      color: #333;
    }

    &.is-textarea {
      width: 100%;
      height: 180px;
      resize: none;
      font-weight: 400;
      font-size: 15px;
      line-height: 1.3;
    }

    &.is-select {
      padding: 0;
      border: none;
      position: relative;
      z-index: 0;

      &::before {
        content: '';
        display: block;
        width: 70px;
        height: calc(100% - 2px);
        position: absolute;
        right: 1px;
        top: 1px;
        z-index: 1;
        background-color: #fff;
        pointer-events: none;
      }

      &::after {
        content: '';
        display: block;
        width: 10px;
        height: 16px;
        position: absolute;
        right: 30px;
        top: calc(50% - 8px);
        z-index: 1;
        background-image: url('/img/select-arrow.svg');
        background-repeat: no-repeat;
        background-position: 50% 50%;
        background-size: contain;
        transform: rotate(90deg);
        pointer-events: none;
      }

      select {
        width: 100%;
        height: 100%;
        padding: 22px 35px;
        color: #333;
        border: 1px solid #c9c9c9;
        font-weight: 600;
        font-size: 18px;
        font-family: $font-family;
        outline: none !important;
        // appearance: none;
        transition: border 400ms ease;

        &:focus {
          border-color: $accent-second;
        }
      }
    }

    &.is-attach {
      padding: 3px;
      position: relative;
      cursor: pointer;

      input {
        width: 100%;
        height: 100%;
        position: absolute;
        left: 0;
        top: 0;
        opacity: 0;
        cursor: pointer;
      }

      &:hover {
        #{$form}__attach-label {
          background-color: #313f74 - 20;
        }
      }

      input:focus + #{$form}__attach #{$form}__attach-label {
        background-color: #313f74 - 20;
      }
    }
  }

  &__attach {
    display: flex;
    align-items: center;

    &-label {
      display: flex;
      justify-content: center;
      align-items: center;
      height: 62px;
      padding-left: 25px;
      padding-right: 30px;
      background-color: #313f74;
      color: #ffffff;
      font-weight: 700;
      font-style: normal;
      font-size: 13px;
      line-height: 1;
      transition: ease 300ms background;

      &::before {
        content: '';
        display: block;
        width: 24px;
        min-width: 24px;
        height: 24px;
        margin-right: 10px;
        background-image: url('/img/attach.svg');
        background-repeat: no-repeat;
        background-position: 50% 50%;
        background-size: contain;
      }
    }

    &-file {
      margin-left: 30px;
      font-weight: 400;
      font-size: 13px;
    }
  }

  &__button {
    margin-top: 50px;
  }

  &__error {
    display: block;
    position: absolute;
    top: calc(100% + 10px);
    color: #e31e24;
    font-size: 12px;
  }

  &__message {
    margin-left: 20px;
    font-size: 13px;
    line-height: 1;
  }

  @media (max-width: $s) {
    &__title {
      font-size: 20px;
    }

    &__label {
      font-size: 13px;
    }

    &__field {
      min-height: 55px;
      padding: 17px 20px;
      font-size: 15px;

      &.is-select {
        select {
          padding: 15px;
          font-size: 15px;
        }

        &::after {
          right: 20px;
        }
      }
    }

    &__attach-file {
      margin-left: 20px;
    }

    &__button {
      margin-top: 30px;
    }
  }
}

/**
* gallery
*/

.gallery {
  $gallery: &;

  max-width: 1040px;
  margin: 80px auto;

  &__tabs {
  }

  &__list {
  }

  &__item {
    display: flex;
    align-items: center;
    justify-content: center;
    height: 100%;
    max-height: 138px;
    position: relative;
    z-index: 0;
    background-color: #d7d7d7;
    text-align: center;
    line-height: 0;

    &.is-video {
      &::before {
        opacity: 0.25;
      }

      &::after {
        content: '';
        display: block;
        position: absolute;
        top: calc(50% - 16px);
        left: calc(50% - 11px);
        z-index: 2;
        border: 16px solid transparent;
        border-left: $accent-second 22px solid;
        opacity: 0.8;
      }
    }

    &::before {
      content: '';
      display: block;
      width: 100%;
      height: 100%;
      position: absolute;
      top: 0;
      left: 0;
      z-index: 1;
      background-color: #333;
      opacity: 0;
      transition: ease opacity 300ms;
    }

    &:hover,
    &:focus {
      &::before {
        opacity: 0.4;
      }
    }

    &:active {
      &::before {
        opacity: 0.6;
      }
    }
  }

  &__preview {
    height: 100%;
    width: 100%;
    max-width: 100%;
    max-height: 100%;
    object-fit: cover;
  }

  @media (max-width: $xl) {
    max-width: 830px;
    margin: 40px auto;
  }

  @media (max-width: $m) {
    margin: 20px auto 40px;
  }
}

/**
* header
*/

.header {
  $header: &;

  display: flex;
  align-items: center;
  position: relative;
  z-index: $layer-1;
  background-color: #fff;
  border-bottom: 1px solid #c9c9c9;

  &__logo {
    width: 420px;
    height: 80px;
    flex: 0 1 420px;
    padding: 10px 75px;
    // padding: 10px 3.8%;
    position: relative;
    z-index: -1;
  }

  &__nav {
  }

  &__group {
    display: flex;
    align-items: center;
    flex-grow: 1;
    position: relative;
    z-index: -1;
  }

  &__contacts {
    display: flex;
    flex: 1 0 77%;
    padding: 0 70px;
    padding: 0 7.5%;
  }

  &__contact {
    & + & {
      margin-left: 95px;
      margin-left: 15%;
    }
  }

  &__search {
    flex: 1 0 33%;
  }

  &__cart {
    position: relative;
    z-index: -1;
  }

  @media (max-width: $xl) {
    &__logo {
      width: 256px;
      flex: 0 1 256px;
      padding: 10px 25px;
    }

    &__contacts {
      padding: 0 5%;
    }

    &__contact {
      & + & {
        margin-left: 10%;
      }
    }
  }

  @media (max-width: $l) {
    &__contacts {
      display: none;
    }

    &__search {
      flex-basis: 100%;
    }
  }

  @media (max-width: $m) {
    flex-flow: row wrap;
    align-items: normal;
    border-bottom: none;

    &__logo {
      max-width: 100%;
      flex: 1 0 100%;
    }
  }
}

/**
* headline
*/

.headline {
  $headline: &;

  margin: 30px 0;
  padding: 0;
  text-align: center;

  &__breadcrumb {
  }

  &__title {
    margin: 0;
    margin-top: 25px;
    font-weight: 600;
    font-size: 32px;
    line-height: 1.25;

    i {
      color: $accent;
      font-style: normal;
    }
  }

  @media (max-width: $l) {
    margin: 20px 0 25px;

    &__title {
      margin-top: 15px;
      padding: 0 10px;
      line-height: 1.15;
    }
  }

  @media (max-width: $m) {
    margin: 15px 0;

    &__title {
      margin-top: 5px;
      font-size: 22px;
    }
  }
}

/**
* logo
*/

.logo {
  $logo: &;

  display: flex;
  align-items: center;
  width: 100%;
  height: 100%;
  position: relative;
  color: $accent-second;
  font-weight: 700;
  font-size: 24px;
  line-height: 1;
  text-decoration: none;
  text-transform: uppercase;
  overflow: hidden;
  cursor: default;

  &.is-link {
    cursor: pointer;
  }

  &::before {
    content: '';
    display: inline-block;
    width: 64px;
    height: 64px;
    min-width: 64px;
    margin-right: 20px;
    background-image: url('/img/logo.png');
    background-repeat: no-repeat;
    background-position: 50% 50%;
    background-size: contain;
    transition: ease 1s transform;
  }

  &__label {
    flex: 1;
    white-space: nowrap;

    i {
      color: $accent;
      font-style: normal;
    }
  }

  &:hover {
    &::before {
      transform: rotate(360deg);
    }
  }

  &:active {
    &::before {
      transform: rotate(0deg);
    }
  }

  @media (max-width: $xl) {
    font-size: 20px;

    &::before {
      width: 54px;
      height: 54px;
      min-width: 54px;
      margin-right: 12px;
    }
  }
}

/**
* map
*/

.map {
  $map: &;

  width: 100%;
  height: 100%;
  min-height: 720px;
  overflow: hidden;
  position: relative;
  z-index: 0;

  &__container {
    width: 100%;
    height: 100%;
    position: absolute !important;
    top: 0;
  }

  &__tooltip {
    display: inline-block;
    min-width: 290px;
    padding: 20px;
    position: absolute;
    background-color: $white;
    border-radius: 3px;
    box-shadow: 0px 5px 20px rgba(0, 0, 0, 0.5);
    color: $black;
    font-family: $font-family;
    opacity: 0;
    visibility: hidden;
    transform: scale(0.75);
    transform-origin: bottom center;
    transition: ease opacity 300ms, ease transform 300ms;

    &::before {
      content: '';
      display: block;
      position: absolute;
      top: calc(100% - 1px);
      right: calc(50% - 5px);
      border: 7px solid $white;
      border-left: 10px solid transparent;
      border-right: 10px solid transparent;
      border-bottom-color: transparent;
    }

    &.is-visible {
      visibility: visible;
      opacity: 1;
      transform: scale(1);
    }

    span {
      display: block;
      margin-bottom: 15px;
      font-weight: 700;
      font-style: normal;
      font-size: 14px;
    }

    ul {
      list-style: none;
      margin: 0;
      margin-bottom: 10px;
      padding: 0;
    }

    li {
      margin: 4px 0;
      color: #243266;
      font-weight: 600;
      font-style: normal;
      font-size: 16px;
    }

    p {
      margin: 0;
      margin-top: 7px;
      font-weight: 400;
      font-style: normal;
      font-size: 13px;
    }
  }

  @media (max-width: $xl) {
    min-height: 550px;
  }

  @media (max-width: $l) {
    min-height: 430px;
  }

  @media (max-width: $md) {
    min-height: 380px;
  }

  @media (max-width: $m) {
    min-height: 326px;
  }
}

/**
* menu
*/

.menu {
  $menu: &;

  &__list {
    margin: 0;
    padding: 0;
    list-style: none;
    color: #fff;

    &.is-level2 {
      display: none;
      max-height: 360px;
      overflow: auto;
      padding-right: 20px;

      &::-webkit-scrollbar {
        width: 5px;
        height: 5px;
      }

      &::-webkit-scrollbar-thumb {
        background-color: #ffffff;
        border-radius: 2px;
      }

      &::-webkit-scrollbar-track {
        margin: 2px 0;
        background-color: #32448d;
      }
    }

    &.is-level3 {
      display: none;
      margin-bottom: 10px;
    }
  }

  &__item {
    margin: 20px 0;

    &.is-level2,
    &.is-level3 {
      margin: 0;
    }

    &.has-child {
      & > #{$menu}__link {
        &::after {
          content: '';
          display: inline-block;
          width: 16px;
          height: 16px;
          position: relative;
          top: 2px;
          background-image: url('/img/menu-arrow.svg');
          background-repeat: no-repeat;
          background-position: 50% 50%;
          background-size: contain;
          transform: rotate(90deg);
          transition: ease transform 300ms;
        }
      }
    }
  }

  &__link {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 10px 0;
    position: relative;
    color: #ffffff;
    font-weight: 600;
    font-style: normal;
    font-size: 15px;
    text-decoration: none;
    transition: ease color 300ms;

    &:hover,
    &:focus,
    &.is-active {
      color: $accent-second;
    }

    &.is-active::after {
      transform: rotate(-90deg) !important;
    }

    &:active {
      color: $accent-second - 20;
    }

    &.is-level2,
    &.is-level3 {
      padding: 5px 0;
      padding-left: 15px;
    }

    &.is-level2 {
      &::before {
        content: '';
        display: block;
        width: 7px;
        height: 15px;
        position: absolute;
        left: 0;
        top: calc(50% - 7px);
        background-color: $accent-second;
      }
    }
  }

  @media (max-width: $xl) {
    &__item {
      margin: 15px 0;
    }
  }
}

/**
* minicart
*/

.minicart {
  $minicart: &;

  height: 80px;
  position: relative;
  z-index: $layer-1;

  &__toggle {
    display: flex;
    align-items: center;
    height: 100%;
    padding: 30px;
    padding-right: 40px;
    position: relative;
    border: none;
    background-color: $accent;
    color: #fff;
    font-weight: 600;
    font-style: normal;
    font-size: 16px;
    text-decoration: none;
    cursor: pointer;
    transition: ease background 300ms;

    &::after {
      content: '';
      width: 100%;
      height: 1px;
      position: absolute;
      top: 100%;
      left: 0;
      background-color: $accent;
    }

    &:hover,
    &:focus,
    &.is-active {
      background-color: #2e3f83;
    }

    &:active {
      background-color: #2e3f83 + 20;
    }

    &-icon {
      display: inline-block;
      width: 24px;
      height: 24px;
      margin-right: 15px;
      position: relative;
      color: #f7a832;
      font-weight: 600;
      font-style: normal;
      font-size: 16px;

      &.in-process {
        &::before {
          opacity: 0;
          transition: ease opacity 300ms, ease-in-out transform 300ms;
          transform: translateY(-5px);
        }

        &::after {
          opacity: 1;
          transform: translateY(0);
          transition: ease-in-out opacity 300ms, ease-in-out transform 300ms;
        }
      }

      &::before {
        content: '';
        width: 100%;
        height: 100%;
        position: absolute;
        left: 0;
        top: 0;
        opacity: 1;
        background-image: url('/img/cart.svg');
        background-repeat: no-repeat;
        background-position: 50% 50%;
        background-size: contain;
        transform: translateY(0);
        transition: ease-in-out opacity 500ms, ease-in-out transform 500ms;
      }

      &::after {
        content: '+' attr(data-amount);
        position: absolute;
        right: 0;
        top: 0;
        opacity: 0;
        transform: translateY(5px);
        transition: ease-in-out opacity 500ms, ease-in-out transform 500ms;
      }
    }

    &-text {
      display: inline-block;
    }
  }

  &__inner {
    width: 320px;
    position: absolute;
    right: 0;
    top: 100%;
    background-color: #ffffff;
    border: 1px solid #c9c9c9;
    opacity: 0;
    visibility: hidden;
    transform: translateY(10px);
    transition: ease 300ms all;

    &.is-visible {
      transform: translateY(0);
      opacity: 1;
      visibility: visible;
    }
  }

  &__body {
    min-height: 120px;
    position: relative;
  }

  &__empty {
    // display: none;
    position: absolute;
    top: 50%;
    left: 50%;
    color: #999;
    font-size: 14px;
    text-align: center;
    line-height: 1.2;
    transform: translate(-50%, -50%);
  }

  &__list {
    min-height: 200px;
    max-height: 400px;
    margin: 0 5px;
    padding: 0 20px;
    overflow: auto;

    &::-webkit-scrollbar {
      width: 4px;
      height: 4px;
    }

    &::-webkit-scrollbar-thumb {
      background: #e4e6f2;
      border-radius: 2px;
    }

    &::-webkit-scrollbar-track {
      background: #fff;
      margin: 5px 0;
    }
  }

  &__item {
    & + & {
      border-top: 1px solid #c9c9c9;
    }
  }

  &__footer {
    display: flex;
    flex-flow: row wrap;
    justify-content: space-between;
    padding: 20px;
    padding-bottom: 25px;
    position: relative;

    &::before {
      content: '';
      display: block;
      width: calc(100% - 40px);
      height: 1px;
      position: absolute;
      top: 0;
      left: 20px;
      background-color: #c9c9c9;
    }
  }

  &__amount {
    color: #333;
    font-weight: 400;
    font-size: 15px;
  }

  &__price {
    display: flex;
    justify-content: space-between;
    font-weight: 700;
    font-style: normal;
    font-size: 16px;
    text-align: right;

    &-label {
      color: #333;
    }

    &-value {
      margin-left: 5px;
      color: #2e3f83;
    }
  }

  &__button {
    margin-top: 20px !important;
  }

  @media (max-width: $l) {
    &__toggle {
      padding: 28px;
    }

    &__toggle-icon {
      margin: 0;
    }

    &__toggle-text {
      display: none;
    }
  }

  @media (max-width: $m) {
    height: 55px;

    &__toggle {
      padding: 20px;
    }

    &__toggle-icon {
      width: 17px;
      height: 17px;
    }
  }
}

/**
* modal
*/

.modal {
  $modal: &;
  $init: '.modal-init';
  $visible: '.modal-visible';

  display: none;
  position: relative;

  &__inner {
    position: relative;
    width: 100%;
    max-width: 680px;
    max-height: 90%;
    padding: 30px;
    padding-bottom: 50px;
    z-index: -1;
    opacity: 0;
    transform: scale(0);
    transition: opacity 0.2s, transform 0.2s, z-index 0s 0.2s;
  }

  &__header {
    padding-right: 30px;
    position: relative;
  }

  &__title {
    margin: 0;
    color: #333;
    font-weight: 500;
    font-size: 20px;
    line-height: 1;
  }

  &__close {
    display: block;
    width: 24px;
    height: 24px;
    padding: 0;
    position: absolute;
    top: 0;
    right: 0;
    border: none;
    background-color: transparent;
    background-image: url('/img/close.svg');
    background-repeat: no-repeat;
    background-position: 50% 50%;
    background-size: contain;
    cursor: pointer;
  }

  &__content {
    margin-top: 20px;
    padding: 0;
  }

  #{$init} & {
    display: block;
    position: fixed;
    content: '';
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-color: rgba(0, 0, 0, 0.7);
    z-index: -1;
    opacity: 0;
    transition: opacity 0.2s, z-index 0s 0.2s;
    text-align: center;
    white-space: nowrap;
    -webkit-overflow-scrolling: touch;
  }

  #{$init} & > * {
    display: inline-block;
    white-space: normal;
    vertical-align: middle;
    text-align: left;
  }

  #{$init} &:before {
    display: inline-block;
    overflow: hidden;
    width: 0;
    height: 100%;
    vertical-align: middle;
    content: '';
  }

  #{$init}#{$visible} & {
    z-index: $layer-2;
    opacity: 1;
    transition: opacity 0.2s;
  }

  #{$visible} &__inner {
    background-color: #f2f2f2;
    z-index: $layer-1;
    opacity: 1;
    transform: scale(1);
    transition: opacity 0.2s, transform 0.2s;
  }
}

/**
* nav
*/

.nav {
  $nav: &;
  $speed: 300ms;

  width: 100%;
  margin: 0;

  &__overlay {
    display: none;
    width: 100vw;
    height: 100vh;
    position: fixed;
    left: 0;
    top: 0;
    right: 0;
    bottom: 0;
    z-index: 0;
    background-color: #fff;
    opacity: 0.5;

    &.is-visible {
      display: block;
    }
  }

  &__toggle {
    display: flex;
    align-items: center;
    height: 80px;
    padding: 28px 32px;
    position: relative;
    z-index: 1;
    border: none;
    background-color: $accent-second;
    color: #333333;
    font-family: $font-family;
    font-weight: 600;
    font-style: normal;
    font-size: 18px;
    cursor: pointer;
    transition: background ease #{$speed}, color ease #{$speed};

    svg {
      width: 24px;
      height: 24px;
      margin-right: 20px;
      color: #000;
      transition: color ease #{$speed};
      pointer-events: none;
    }

    span {
      pointer-events: none;
    }

    &:hover,
    &:focus {
      background-color: $accent-second + 20;
    }

    &:active {
      background-color: $accent-second;
    }

    &.is-active {
      background-color: #000;
      color: #fff;

      svg {
        color: $accent-second;
      }
    }

    &::after {
      content: '';
      width: 100%;
      height: 1px;
      position: absolute;
      top: 100%;
      left: 0;
      background-color: $accent-second;
    }
  }

  &__list {
    display: flex;
    align-items: center;
    width: 100%;
    margin: 0;
    padding: 0 60px;
    position: absolute;
    left: 0;
    z-index: 1;
    opacity: 0;
    visibility: hidden;
    background-color: #000000;
    list-style: none;
    transition: ease opacity #{$speed}, ease visibility #{$speed};

    &.is-open {
      opacity: 1;
      visibility: visible;
    }
  }

  &__item {
    position: relative;

    & + & {
      margin-left: 30px;
    }
  }

  &__link {
    display: block;
    padding: 27.5px;
    font-weight: 600;
    font-size: 17px;
    color: #ffffff;
    text-decoration: none;
    white-space: nowrap;
    transition: ease color #{$speed};

    &:hover,
    &:focus {
      color: $accent-second;
    }

    &:active {
      color: #656d75;
    }
  }

  @media (max-width: $m) {
    &__toggle {
      height: 55px;
      padding: 16px 20px;

      svg {
        width: 17px;
        height: 17px;
        margin-right: 15px;
      }
    }
  }

  @media (max-width: $xl) {
    &__list {
      padding: 0;
    }
  }

  @media (max-width: $l) {
    &__item + &__item {
      margin: 0;
    }
  }

  @media (max-width: 900px) {
    &__list {
      flex-flow: row wrap;
      padding: 27.5px;
    }

    &__item {
      width: 100%;

      & + & {
        margin-top: 15px;
      }
    }

    &__link {
      padding: 0;
    }
  }
}

/**
* order
*/

.order {
  $order: &;

  &__section {
    // padding: 0 80px;
    padding: 0 5%;

    & + & {
      margin-top: 75px;
    }

    &.is-wide {
      padding: 0;
    }

    &.is-question {
      width: 70%;
      padding-top: 40%;
      margin: auto;
      position: relative;
      overflow: hidden;
      background-color: #313f74;

      &::before {
        content: attr(data-label);
        max-width: 200px;
        position: absolute;
        left: 10%;
        top: 50%;
        color: #ffffff;
        font-weight: 700;
        font-style: normal;
        font-size: 24px;
        line-height: 1.3;
        transform: translateY(-50%);
      }

      &::after {
        content: '';
        display: block;
        width: 60%;
        height: 85%;
        position: absolute;
        bottom: 0;
        left: calc(50% + 80px);
        transform: translateX(-50%);
        background-image: url('/img/order-question.svg');
        background-repeat: no-repeat;
        background-position: 50% 100%;
        background-size: contain;
      }
    }
  }

  &__title {
    margin: 0;
    padding-left: 20px;
    padding-right: 20px;
    color: #333;
    font-weight: 600;
    font-size: 24px;
    line-height: 1.2;
    text-align: center;
  }

  &__body {
    margin-top: 40px;
  }

  &__box {
    display: flex;
    align-items: center;
    width: 100%;
    height: 414px;
    position: relative;
    overflow: hidden;
    background-color: #f7a832;
  }

  &__group {
    display: flex;
    align-items: center;
    justify-content: flex-end;
    flex-direction: column;
    width: 50%;
    height: 100%;
    padding-left: 20px;
    position: relative;

    &::before {
      content: '';
      display: block;
      width: 192px;
      height: 129px;
      // position: absolute;
      // top: 24%;
      // left: 15%;
      background-image: url('/img/order-cupboard.svg');
      background-repeat: no-repeat;
      background-position: 50% 50%;
      background-size: contain;
    }
  }

  &__button {
    margin-top: 40px;
    margin-bottom: 90px;
    position: relative;

    &::after {
      content: '';
      display: block;
      width: 57px;
      height: 112px;
      position: absolute;
      left: 110px;
      bottom: -90px;
      background-image: url('/img/order-hand.svg');
      background-repeat: no-repeat;
      background-position: 50% 50%;
      background-size: contain;
      pointer-events: none;
    }
  }

  &__label {
    width: 50%;
    padding-right: 20%;
    color: #333;
    font-weight: 700;
    font-style: normal;
    font-size: 24px;
    line-height: 1.3;
  }

  &__text {
    padding: 35px;
  }

  &__form {
    padding: 80px;
    padding-bottom: 100px;
    text-align: center;
  }

  @media (max-width: 1600px) {
    &__label {
      padding-left: 10px;
      padding-right: 5%;
    }

    &__box {
      width: calc(100% - 20px);
      height: 370px;
    }
  }

  @media (max-width: $xl) {
    &__section {
      padding: 0 40px;

      & + & {
        margin-top: 60px;
      }
    }

    &__form {
      padding: 40px;
      padding-bottom: 60px;
    }
  }

  @media (max-width: $l) {
    &__section {
      &.is-question {
        width: 90%;
        padding-top: 50%;

        &::before {
          font-size: 20px;
        }
      }
    }

    &__box {
      width: 100%;
    }

    &__text {
      padding: 35px 0 0;
    }
  }

  @media (max-width: $md) {
    &__group {
      &::before {
        width: 132px;
        height: 90px;
      }
    }

    &__box {
      width: 100%;
      height: 256px;
    }

    &__label {
      padding-left: 0;
      padding-right: 10%;
      font-size: 20px;
    }

    &__button {
      min-width: 106px !important;
      margin-top: 20px;
      margin-bottom: 60px;
      padding: 13px;
      font-size: 14px;

      &::after {
        width: 39px;
        height: 77px;
        left: 75px;
        bottom: -65px;
      }
    }

    &__text {
      padding: 0 30px;
    }
  }

  @media (max-width: $s) {
    &__section {
      padding: 0;

      &.is-question {
        width: 100%;
        padding-top: 55%;

        &::before {
          max-width: 160px;
          left: 5%;
          top: 15%;
          font-size: 18px;
          transform: translateY(0);
        }

        &::after {
          width: 150%;
          height: 160%;
          bottom: -75%;
          left: -31%;
          transform: translateX(0);
        }
      }

      & + & {
        margin-top: 35px;
      }
    }

    &__body {
      margin-top: 30px;
    }

    &__title {
      font-size: 18px;
    }

    &__label {
      padding-left: 20px;
      padding-right: 20px;
      font-size: 16px;
    }

    &__form {
      padding: 20px;
      padding-bottom: 40px;
    }
  }
}

/**
* order-button
*/

.order-button {
  $order-button: &;

  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 18px 30px;
  border: 3px solid $accent-second;
  color: #fff;
  text-decoration: none;
  transition: ease color 300ms, ease background 300ms;

  &__icon {
    width: 48px;
    height: 48px;
    flex: 0 0 48px;
    margin-right: 30px;
    color: currentColor;
  }

  &__text {
    font-weight: 600;
    font-style: normal;
    font-size: 15px;
    line-height: 20px;

    i {
      font-style: normal;
    }
  }

  &:hover,
  &:focus {
    color: #333333;
    background-color: $accent-second;
  }

  &:active {
    background-color: $accent-second + 20;
  }

  @media (max-width: $xl) {
    padding: 10px;

    &__icon {
      width: 42px;
      height: 42px;
      flex-basis: 42px;
      margin-right: 10px;
    }

    &__text {
      font-size: 13px;

      i {
        display: block;
      }
    }
  }
}

/**
* order-form
*/

.order-form {
  $order-form: &;

  display: inline-block;
  width: 100%;
  max-width: 500px;
}

/**
* page
*/

.page {
  $page: &;

  display: flex;
  flex-flow: row wrap;
  width: 100%;
  min-width: $minwidth;
  max-width: $maxwidth;
  height: 100vh;
  margin: 0 auto;
  position: relative;
  overflow: hidden;
  background-color: #fff;
  box-shadow: 0 0 15px #777;

  &__header {
    width: 100%;
    flex-grow: 1;
  }

  &__sidebar {
    flex: 0 0 420px;
    width: 420px;
    height: calc(100% - 80px);
    background-color: #243266;
  }

  &__content {
    display: flex;
    flex-direction: column;
    flex: 1 0 auto;
    max-width: calc(100% - 420px);
    height: calc(100% - 80px);
    min-height: calc(100% - 80px);
    overflow-y: scroll;
    overflow-x: hidden;
    position: relative;

    &::-webkit-scrollbar {
      width: 4px;
      height: 4px;
    }

    &::-webkit-scrollbar-thumb {
      background-color: #999;
    }

    &::-webkit-scrollbar-track {
      background-color: transparent;
    }
  }

  &__footer {
    width: 100%;
  }

  @media (max-width: $xl) {
    &__sidebar {
      flex: 0 0 256px;
      width: 256px;
    }

    &__content {
      max-width: calc(100% - 256px);
    }
  }

  @media (max-width: $md) {
    &__sidebar {
      display: none;
    }
    &__content {
      max-width: 100%;
    }
  }

  @media (max-width: $m) {
    &__content {
      height: calc(100% - 135px);
      min-height: calc(100% - 135px);
    }
  }
}

/**
* pagination
*/

.pagination {
  $pagination: &;

  width: 100%;

  &__inner {
    display: flex;
    align-items: center;
    justify-content: space-between;
  }

  &__list {
    display: flex;
    align-items: center;
    flex-flow: row wrap;
    padding: 0;
    margin: -7.5px;
    list-style: none;
  }

  &__item {
    margin: 7.5px;

    &.is-current {
      #{$pagination}__link {
        border-color: #000000;
        background-color: #000000;
        color: #fff;
      }
    }
  }

  &__link {
    position: relative;
    display: block;
    width: 48px;
    height: 48px;
    color: #333;
    border: 1px solid #c8cedd;
    font-weight: 400;
    font-weight: 400;
    font-size: 15px;
    line-height: 46px;
    text-align: center;
    text-decoration: none;
    transition: ease border 300ms;
    z-index: 0;

    &:hover,
    &:focus {
      border-color: $accent;
    }

    &:active {
      border-color: #333;
    }
  }

  &__amount {
    color: #999;
    font-weight: 400;
    font-size: 15px;
    white-space: nowrap;
    margin-left: 20px;
  }

  @media (max-width: $l) {
    &__amount {
      font-size: 13px;
    }
  }

  @media (max-width: $m) {
    &__amount {
      display: none;
    }

    &__link {
      width: 45px;
      height: 45px;
      line-height: 43px;
    }
  }
}

/**
* portfolio
*/

.portfolio {
  $portfolio: &;

  &.is-main {
    #{$portfolio}__filter {
      position: relative;
      border-bottom: 1px solid #c9c9c9;
      background-color: #fff;
      z-index: 1;
    }

    #{$portfolio}__content {
    }

    #{$portfolio}__list {
      display: flex;
      flex-flow: row wrap;

      @supports (display: grid) {
        display: grid;
        grid-template-columns: repeat(4, 1fr);
      }
    }

    #{$portfolio}__item {
      width: 25%;

      @supports (display: grid) {
        width: 100%;

        &:nth-child(14n + 5),
        &:nth-child(14n + 12) {
          grid-column: span 2;
          grid-row: span 2;
          height: 556px;

          .portfolio-item__title {
            max-width: 340px;
            margin: auto;
            padding: 0;
            font-size: 28px;
            line-height: 1.4;
          }
        }
      }
    }
  }

  &__list {
  }

  &__item {
    height: 278px;
  }

  &__pagination,
  &__text {
    margin: 60px 0;
    padding: 0 80px;
  }

  @media (max-width: $xl) {
    &.is-roll {
      #{$portfolio}__item:nth-child(3) ~ #{$portfolio}__item {
        display: none;
      }
    }

    &.is-main {
      #{$portfolio}__list {
        @supports (display: grid) {
          grid-template-columns: repeat(3, 1fr);
        }
      }

      #{$portfolio}__item {
        width: 33.33%;

        @supports (display: grid) {
          width: 100%;

          &:nth-child(14n + 5),
          &:nth-child(14n + 12) {
            grid-column: initial;
            grid-row: initial;
            height: 278px;

            .portfolio-item__title {
              font-size: 15px;
            }
          }

          &:nth-child(12n + 5),
          &:nth-child(12n + 10) {
            grid-column: span 2;
            grid-row: span 2;
            height: 556px;

            .portfolio-item__title {
              font-size: 28px;
            }
          }
        }
      }
    }

    &__pagination {
      margin: 50px 0;
      padding-left: 40px;
      padding-right: 40px;
    }

    &__text {
      margin: 50px 0;
      padding-left: 40px;
      padding-right: 40px;
    }
  }

  @media (max-width: $l) {
    &__item {
      height: 194px;
    }

    &.is-main {
      #{$portfolio}__item {
        @supports (display: grid) {
          &:nth-child(14n + 5),
          &:nth-child(14n + 12) {
            height: 194px;
          }

          &:nth-child(12n + 5),
          &:nth-child(12n + 10) {
            height: 388px;

            .portfolio-item__title {
              font-size: 20px;
            }
          }
        }
      }
    }

    &__pagination {
      margin: 45px 0;
    }

    &__text {
      margin: 45px 0;
    }
  }

  @media (max-width: $s) {
    &.is-main {
      #{$portfolio}__list {
        @supports (display: grid) {
          grid-template-columns: repeat(1, 1fr);
        }
      }

      #{$portfolio}__item {
        width: 100%;

        &:first-child {
          margin-top: 0;
        }

        .portfolio-item__title {
          font-size: 15px !important;
        }

        @supports (display: grid) {
          grid-column: initial !important;
          grid-row: initial !important;
        }
      }
    }

    &.is-roll {
      #{$portfolio}__item {
        &:first-child {
          margin-top: 10px;
        }

        &:nth-child(3) {
          margin-bottom: 10px;
        }
      }
    }

    &__item {
      margin: 5px 0;
      height: 237px !important;
    }

    &__filter {
      display: none;
    }

    &__pagination {
      margin: 40px 0;
      padding-left: 20px;
      padding-right: 20px;
    }

    &__text {
      margin: 40px 0;
      padding-left: 20px;
      padding-right: 20px;
    }
  }
}

/**
* portfolio-card
*/

.portfolio-card {
