/**
* portfolio-item
*/

.portfolio-item {
  $portfolio-item: &;

  display: block;
  width: 100%;
  height: 100%;
  position: relative;

  &.is-yellow {
    #{$portfolio-item}__cover::after {
      opacity: 0.75;
      background-color: #e88e09;
    }
  }

  &.is-blue {
    #{$portfolio-item}__cover::after {
      opacity: 0.75;
      background-color: $accent;
    }
  }

  &__cover {
    width: 100%;
    height: 100%;
    position: relative;
    z-index: 0;
    background-repeat: no-repeat;
    background-position: 50% 50%;
    background-size: cover;

    &::before {
      content: '';
      display: block;
      width: 36px;
      height: 36px;
      position: absolute;
      top: 15px;
      right: 24px;
      z-index: 1;
      background-image: url('/img/portfolio-arrow.svg');
      background-repeat: no-repeat;
      background-position: 50% 50%;
      background-size: contain;
      opacity: 0;
      transform: translateX(-10px);
      transition: ease opacity 300ms, ease transform 300ms;
    }

    &::after {
      content: '';
      display: block;
      width: 100%;
      height: 100%;
      position: absolute;
      left: 0;
      top: 0;
      background-color: #000;
      opacity: 0.5;
      transition: ease opacity 300ms, ease background 300ms;
    }
  }

  &__content {
    display: flex;
    align-items: center;
    width: 100%;
    height: 100%;
    position: absolute;
    left: 0;
    top: 0;
  }

  &__title {
    width: 100%;
    max-height: 120px;
    padding: 0 50px;
    position: relative;
    top: -10px;
    color: #ffffff;
    font-size: 18px;
    font-weight: 700;
    font-style: normal;
    text-align: center;
    line-height: 1.6;
    transition: ease color 300ms;
  }

  &__tag {
    display: inline-block;
    position: absolute;
    bottom: 30px;
    left: 50%;
    padding: 7px 20px;
    border: 1px solid currentColor;
    border-radius: 100px;
    color: #ffffff;
    font-weight: 400;
    font-style: normal;
    font-size: 11px;
    line-height: 1;
    transform: translateX(-50%);
    transition: ease color 300ms;
  }

  &:hover,
  &:focus {
    #{$portfolio-item}__title,
    #{$portfolio-item}__tag {
      color: #000;
    }

    #{$portfolio-item}__cover::before {
      opacity: 1;
      transform: translateX(0);
    }

    #{$portfolio-item}__cover::after {
      background-color: #f7a832;
      opacity: 1;
    }
  }

  &:active {
  }

  @media (max-width: $l) {
    &__title {
      font-size: 15px;
      padding: 0 10px;
    }
    &__tag {
      // display: none;
    }
  }

  @media (max-width: $m) {
    &__cover::after {
      background-color: #000 !important;
      opacity: 0.5 !important;
    }

    &:hover,
    &:focus {
      #{$portfolio-item}__cover::after {
        background-color: #f7a832 !important;
        opacity: 1 !important;
      }
    }
  }

  @media (max-width: $s) {
    &__title {
      padding: 0 50px;
    }
    &__tag {
      // display: inline-block;
    }
  }
}

/**
* product
*/

.product {
  $product: &;

  display: block;
  width: 374px;
  height: 100%;
  margin-bottom: 60px;
  margin-left: 10px;
  margin-right: 10px;
  text-decoration: none;

  &.is-fold {
    width: 100%;
    margin: 0;

    #{$product}__body {
      border-top: none;
      border-left: none;
    }
  }

  &.is-set {
    width: 100%;
    margin: 0;

    #{$product}__body {
      border-top: none;
      border-left: none;
      border-bottom: none;
    }
  }

  &__cover {
    width: 100%;
    height: 280px;
    position: relative;
    z-index: 0;
    background-color: #ddd;

    &::after {
      content: '';
      display: block;
      width: 140px;
      height: 100%;
      position: absolute;
      left: 0;
      top: 0;
      z-index: 2;
      opacity: 0.65;
      background-image: linear-gradient(90deg, #000 0%, rgba(0, 0, 0, 0) 90%);
    }
  }

  &__image {
    width: 100%;
    height: 100%;
    max-width: 100%;
    max-height: 100%;
    position: absolute;
    top: 50%;
    left: 50%;
    z-index: 1;
    object-fit: cover;
    transform: translate(-50%, -50%);
  }

  &__overlay {
    width: 100%;
    height: 100%;
    position: absolute;
    top: 0;
    left: 0;
    z-index: 3;
    background-color: rgba(247, 168, 50, 0.85);
    opacity: 0;
    transition: ease opacity 300ms;

    &::before {
      content: '';
      display: block;
      width: 36px;
      height: 36px;
      position: absolute;
      top: 15px;
      right: 24px;
      z-index: 1;
      background-image: url('/img/portfolio-arrow.svg');
      background-repeat: no-repeat;
      background-position: 50% 50%;
      background-size: contain;
      opacity: 1;
      transform: translateX(-10px);
      transition: ease transform 300ms;
    }

    &::after {
      content: '';
      position: absolute;
      left: 50%;
      top: 50%;
      color: #333;
      font-weight: 700;
      font-size: 22px;
      transform: translate(-50%, -50%);
    }
  }

  &__body {
    height: calc(100% - 280px);
    padding: 30px;
    padding-bottom: 25px;
    position: relative;
    background-color: #fff;
    border: 1px solid #c9c9c9;

    &::before {
      content: '';
      width: 40px;
      height: 10px;
      position: absolute;
      top: calc(0% - 5px);
      left: calc(50% - 20px);
      background-color: #f7a832;
      transition: ease background 300ms;
    }
  }

  &__title {
    min-height: 40px;
    margin: 0;
    color: #333;
    font-weight: 400;
    font-size: 15px;
    text-align: center;
  }

  &__price {
    margin-top: 20px;
    text-align: center;

    &-new {
      display: inline-block;
      position: relative;
      color: #313f74;
      font-weight: 700;
      font-size: 18px;
      line-height: 1;
    }

    &-old {
      position: absolute;
      top: 2px;
      left: calc(100% + 20px);
      color: #333;
      font-weight: 400;
      font-size: 14px;
      white-space: nowrap;
      text-decoration: line-through;
    }
  }

  &:hover,
  &:focus {
    #{$product}__overlay {
      opacity: 1;

      &::before {
        transform: translateX(0);
      }
    }

    #{$product}__body {
      &::before {
        background-color: #333333;
      }
    }
  }

  &:active {
  }

  @media (max-width: $l) and (min-width: $s) {
    &__cover {
      height: 195px;
    }

    &__body {
      height: calc(100% - 195px);
      padding: 20px;
      padding-bottom: 10px;
    }

    &__price {
      margin-top: 5px;

      &-old {
        display: none;
      }
    }
  }

  @media (max-width: $s) {
    &__cover {
      height: 240px;
    }

    &__body {
      height: calc(100% - 240px);
      padding-bottom: 20px;
    }

    &__price {
      margin-top: 15px;
    }
  }
}

/**
* product-card
*/

.product-card {
  $product-card: &;

  &__inner {
    min-height: 660px;
  }

  &__slider {
  }

  &__order {
    border-left: 1px solid #c9c9c9;
  }

  &__features {
    display: flex;
    padding: 0 20px;
    border-top: 1px solid #c9c9c9;
  }

  &__feature {
    padding: 30px 60px;
    padding-right: 40px;

    & + & {
      border-left: 1px solid #c9c9c9;
    }
  }

  &__share {
    padding: 10px 80px;
    border-top: 1px solid #c9c9c9;
  }

  @media (max-width: $l) {
    &__features {
      padding: 0;
    }

    &__feature {
      padding: 30px 40px;
    }

    &__share {
      padding: 10px 40px;
    }
  }

  @media (max-width: $s) {
    &__features {
      flex-flow: row wrap;
    }

    &__feature {
      width: 100%;
      padding: 30px 20px;

      & + & {
        border-left: none;
        border-top: 1px solid #c9c9c9;
      }
    }

    &__share {
      padding: 10px 20px;
    }
  }
}

/**
* product-color
*/

.product-color {
  $product-color: &;

  display: flex;
  align-items: center;
  flex-flow: row wrap;
  margin: -5px;

  &__item {
    position: relative;
    margin: 5px;
  }

  &__field {
    opacity: 0;
    position: absolute;
    left: 0;
    top: 0;
  }

  &__cover {
    width: 70px;
    height: 70px;
    position: relative;
    border-radius: 50%;
    overflow: hidden;
    cursor: pointer;

    &::after {
      content: '';
      width: 100%;
      height: 100%;
      position: absolute;
      top: 0;
      left: 0;
      opacity: 0;
      border-radius: 50%;
      border: 5px solid #f7a832;
      transition: opacity ease 300ms;
    }
  }

  &__image {
    width: 100%;
    height: 100%;
    padding: 1px;
    object-fit: cover;
  }

  &__tooltip {
    padding: 8px 40px;
    position: absolute;
    left: 50%;
    bottom: calc(100% + 5px);
    opacity: 0;
    background-color: #e4e4e4;
    border-radius: 3px;
    color: #333;
    box-shadow: rgba(0, 0, 0, 0.498) 0px 5px 20px;
    transform: translateX(-50%) translateY(10px);
    transition: ease transform 300ms, ease opacity 300ms;
    transition-delay: 300ms;

    &::before {
      content: '';
      display: block;
      position: absolute;
      top: 100%;
      left: calc(50% - 10px);
      border: 10px solid transparent;
      border-bottom-width: 8px;
      border-top: 8px solid #e4e4e4;
    }

    &::after {
      content: attr(data-title);
      font-weight: 400;
      font-style: normal;
      font-size: 12px;
    }
  }

  &__item:hover &__tooltip,
  &__field:focus ~ &__tooltip {
    opacity: 1;
    transform: translateX(-50%) translateY(0);
  }

  &__field:checked ~ &__cover::after {
    opacity: 1;
  }

  &__field:disabled ~ &__cover {
    cursor: default;
  }

  @media (max-width: $s) {
    margin: -3px;

    &__item {
      margin: 3px;
    }

    &__cover {
      width: 50px;
      height: 50px;
    }
  }
}

/**
* product-feature
*/

.product-feature {
  $product-feature: &;

  display: flex;
  flex-flow: row wrap;
  align-items: center;

  &__image {
    width: 100%;
    height: 100%;
    max-width: 110px;
    max-height: 105px;
    margin-right: 30px;
    object-fit: contain;
  }

  &__title {
    margin: 0;
    margin: 20px 0 0;
    max-width: 110px;
    font-weight: 600;
    font-size: 15px;
    color: #333;
  }

  &__descr {
    width: 100%;
    margin: 0;
    margin-top: 20px;
    font-weight: 400;
    font-size: 13px;
    line-height: 2;
  }

  @media (max-width: $s) {
    &__image {
      margin-right: 20px;
    }
  }
}

/**
* product-order
*/

.product-order {
  $product-order: &;

  padding: 45px 80px 60px;

  .js-init & {
    &__counter,
    &__group.is-color {
      opacity: 0;
      transition: ease opacity 300ms;
    }

    &__button {
      opacity: 0;
    }

    &.is-visible {
      #{$product-order}__counter,
      #{$product-order}__button,
      #{$product-order}__group.is-color {
        opacity: 1;
      }
    }
  }

  &__section {
    & + & {
      margin-top: 45px;
    }
  }

  &__title {
    margin: 0;
    color: #333;
    font-weight: 700;
    font-size: 18px;
    line-height: 1.2;
  }

  &__title + &__group {
    margin-top: 30px;
  }

  &__price {
    flex-grow: 1;
    color: #333;

    &-label {
      font-weight: 400;
      font-size: 13px;
      line-height: 1;
    }

    &-value {
      margin-top: 5px;
      font-weight: 700;
      font-size: 32px;
      line-height: 1;
    }
  }

  &__counter {
    margin: 0 25px;
  }

  &__button {
    min-width: 180px;
    margin: 15px 0;

    &.is-link {
      display: none;
      background-color: $accent;
      color: #fff;

      &:hover,
      &:focus {
        background-color: $accent + 20;
      }

      &:active {
        background-color: $accent - 20;
      }
    }
  }

  .js-init &__button.is-link {
    display: inline-block;
  }

  @media (max-width: $l) {
    padding: 20px 40px 60px;
  }

  @media (max-width: $s) {
    padding: 20px 20px 40px;

    &__price {
      width: 100%;

      &-value {
        font-size: 20px;
      }
    }

    &__counter {
      margin: 20px 0;
    }

    &__button {
      min-width: auto;
      margin: 20px 0;
      margin-left: 15px;

      &.is-link {
        margin-left: 0;
      }
    }
  }
}

/**
* product-set
*/

.product-set {
  $product-set: &;

  opacity: 1 !important;

  &__header {
    display: flex;
    align-items: center;
    text-decoration: none;

    &:hover,
    &:focus {
      #{$product-set}__title {
        color: #246eb4;
      }
    }

    &:active {
      #{$product-set}__title {
        color: #246eb4 + 20;
      }
    }
  }

  &__cover {
    width: 200px;
    height: 150px;
    flex: 0 1 auto;
    overflow: hidden;
    // background-color: #ddd;
    line-height: 0;
  }

  &__image {
    width: 100%;
    height: 100%;
    max-width: 100%;
    max-height: 100%;
    object-fit: contain;
    object-position: left;
  }

  &__title {
    margin: 0;
    margin-left: 40px;
    color: #bcbcbc;
    font-weight: 600;
    font-style: normal;
    font-size: 28px;
    transition: ease color 300ms;
  }

  &__list {
    margin-top: 30px !important;
  }

  @media (max-width: $s) {
    &__header {
      flex-flow: row wrap;
    }

    &__title {
      margin: 0;
      margin-top: 25px;
      font-size: 20px;
    }

    &__cover {
      width: 100%;
      height: 100%;
      // background-color: #fff;
    }

    &__image {
      max-height: 240px;
    }
  }
}

/**
* product-size
*/

.product-size {
  $product-size: &;

  display: flex;

  &__item {
    color: #333;
    font-weight: 400;
    font-size: 13px;
    text-align: center;

    & + & {
      margin-left: 40px;
    }

    &::before {
      content: '';
      display: block;
      width: 60px;
      height: 60px;
      margin-bottom: 10px;
      background-image: url('/img/size-width.svg');
      background-repeat: no-repeat;
      background-position: 50% 50%;
      background-size: contain;
    }

    &.is-width {
      &::before {
        background-image: url('/img/size-width.svg');
      }
    }

    &.is-height {
      &::before {
        background-image: url('/img/size-height.svg');
      }
    }

    &.is-depth {
      &::before {
        background-image: url('/img/size-depth.svg');
      }
    }
  }
}

/**
* product-slider
*/

.product-slider {
  $product-slider: &;

  padding-bottom: 100px;

  &__inner {
    width: 100%;
    height: 0;
    z-index: 0;
    opacity: 0;
    transition: ease opacity 300ms;
  }

  &.is-inited &__inner {
    opacity: 1;
    height: 100%;
    position: relative;
  }

  &__arrow {
    display: inline-block;
    width: 20px;
    height: 32px;
    padding: 0;
    position: absolute;
    top: calc(50% - 16px);
    left: 25px;
    z-index: 1;
    border: none;
    background-color: transparent;
    background-image: url('/img/slider-arrow.svg');
    background-repeat: no-repeat;
    background-position: 50% 50%;
    background-size: contain;
    opacity: 0.4;
    transition: transform ease 300ms;
    cursor: pointer;

    &.is-prev {
      transform: rotate(180deg);
    }

    &.is-next {
      left: auto;
      right: 25px;
    }

    &:hover,
    &:focus {
      &.is-prev {
        transform: rotate(180deg) scale(1.1);
      }

      transform: scale(1.1);
    }

    &:active {
      &.is-prev {
        transform: rotate(180deg) scale(1);
      }

      transform: scale(1);
    }
  }

  &__container {
    width: 100%;
    border-bottom: 1px solid #c9c9c9;
    background-color: #e7e7e7;
    line-height: 0;
  }

  &__slide {
    width: 100%;
    height: 560px;
    text-align: center;
    background-repeat: no-repeat;
    background-position: 50% 50%;
    background-size: contain;
  }

  .slick-dots {
    display: flex;
    margin: 0;
    padding: 0;
    position: absolute;
    top: calc(100% + 40px);
    left: 50%;
    list-style: none;
    transform: translateX(-50%);

    li {
      margin: 0 8px;
    }

    li.slick-active button {
      background-color: #313f74;
    }

    button {
      display: inline-block;
      width: 14px;
      height: 14px;
      padding: 0;
      background-color: #cccccc;
      border: none;
      border-radius: 50%;
      overflow: hidden;
      color: transparent;
      text-indent: -9999px;
      cursor: pointer;
      transition: ease background 300ms;
    }
  }

  @media (max-width: $xl) {
    &__slide {
      height: 276px;
    }
  }

  @media (max-width: $l) {
    padding-left: 15px;
    padding-right: 15px;

