/**
* set
*/

.set {
  $set: &;

  .js-init & {
    opacity: 0;
    transition: ease opacity 300ms;
  }

  .js-init &__button.is-link {
    display: inline-block;
  }

  &.is-visible {
    opacity: 1;
  }

  &.is-added {
    #{$set}__list {
      opacity: 0.6;
    }
    #{$set}__item {
      pointer-events: none;
    }
  }

  &__cover {
    text-align: center;
    line-height: 0;
    border-bottom: 1px solid #c9c9c9;
  }

  &__image {
    max-width: 100%;
    max-height: 100%;
  }

  &__body {
    padding-top: 20px;
  }

  &__section {
    margin-top: 60px;

    &.is-color {
      width: 100%;
      flex-grow: 1;
      margin-top: 40px;
      margin-bottom: 60px;

      #{$set}__group {
        padding: 0;
      }

      #{$set}__title {
        padding: 0;
      }
    }
  }

  &__group {
    display: flex;
    flex-flow: row wrap;
    align-items: flex-start;
    justify-content: space-between;
    margin-top: 40px;
    padding-left: 80px;
    padding-right: 80px;

    &.is-products {
      padding: 0;
      align-items: inherit;
      justify-content: flex-start;
    }

    &.is-order {
      margin: -30px 0;
    }
  }

  &__title {
    margin: 0;
    padding-left: 80px;
    padding-right: 80px;
    color: #333;
    font-size: 18px;
    font-weight: 700;
  }

  &__list {
    flex-grow: 1;
    max-width: 55%;
    margin: 30px 0;
    margin-right: 100px;

    &.is-numeric {
      counter-reset: n;
      margin: 0;
      padding: 0;
      list-style: none;

      #{$set}__item {
        cursor: default;

        &::before {
          content: counter(n);
          counter-increment: n;
          width: 24px;
          height: 24px;
          margin-right: 18px;
          color: #f7a832;
          border: 1px solid #c9c9c9;
          border-radius: 2px;
          font-weight: 700;
          font-size: 16px;
          text-align: center;
        }
      }
    }
  }

  &__item {
    display: flex;
    align-items: flex-start;
    position: relative;
    color: #333;
    font-weight: 400;
    font-size: 15px;
    cursor: pointer;

    & + & {
      margin-top: 15px;
    }

    &.is-current {
      font-weight: 700;
    }

    &-title {
      flex-grow: 1;
      margin-right: 20px;
    }

    &-price {
      white-space: nowrap;
    }

    &-field {
      opacity: 0;
      position: absolute;
    }

    &-checkbox {
      display: block;
      width: 24px;
      height: 24px;
      min-width: 24px;
      margin-right: 18px;
      position: relative;
      border: 1px solid #c9c9c9;
      border-radius: 2px;
      transition: ease border 300ms;

      &::after {
        content: '';
        width: 16px;
        height: 16px;
        position: absolute;
        top: calc(50% - 8px);
        left: calc(50% - 8px);
        background-image: url('/img/checked.svg');
        background-repeat: no-repeat;
        background-position: 50% 50%;
        background-size: contain;
        opacity: 0;
        transition: ease opacity 300ms;
      }
    }

    &:hover &-checkbox,
    &-field:focus + &-checkbox {
      border: 1px solid $accent-second;
    }

    &-field:checked + &-checkbox::after {
      opacity: 1;
    }
  }

  &__order {
    display: flex;
    flex-grow: 1;
    margin: 30px 0;
  }

  &__price {
    width: 170px;
    margin: 0 80px;
    margin-left: 0;
    position: relative;
    color: #333;

    &-label {
      font-weight: 400;
      font-size: 13px;
      line-height: 1;
    }

    &-value {
      margin-top: 5px;
      font-weight: 700;
      font-size: 32px;
      line-height: 1;
      white-space: nowrap;
    }
  }

  &__button {
    min-width: 180px !important;

    &.is-link {
      display: none;
      background-color: $accent;
      color: #fff;

      &:hover,
      &:focus {
        background-color: $accent + 20;
      }

      &:active {
        background-color: $accent - 20;
      }
    }
  }

  &__product {
    border-bottom: 1px solid #c9c9c9;
  }

  &__product:nth-child(4n + 1):nth-last-child(1),
  &__product:nth-child(4n + 1):nth-last-child(2),
  &__product:nth-child(4n + 1):nth-last-child(3),
  &__product:nth-child(4n + 1):nth-last-child(4),
  &__product:nth-child(4n + 1):nth-last-child(1) ~ &__product,
  &__product:nth-child(4n + 1):nth-last-child(2) ~ &__product,
  &__product:nth-child(4n + 1):nth-last-child(3) ~ &__product,
  &__product:nth-child(4n + 1):nth-last-child(4) ~ &__product {
    border-bottom: none;
  }

  @media (max-width: 1600px) {
    &__list {
      width: 100%;
      max-width: 700px;
      margin-right: 0;
    }

    &__item-title {
      margin-right: 60px;
    }
  }

  @media (max-width: $xl) {
    &__body {
      padding: 0;
    }

    &__section {
      margin-top: 50px;
    }

    &__group {
      padding-right: 40px;
      padding-left: 40px;
    }

    &__title {
      padding-left: 40px;
      padding-right: 40px;
    }

    &__group.is-order {
      align-items: flex-end;
    }

    &__list {
      order: 1;
    }

    &__order {
      order: 3;
      align-items: flex-end;
      justify-content: flex-end;
    }

    &__section.is-color {
      width: auto;
      margin: 30px 0;
      order: 2;
      max-width: 45%;
    }

    &__price {
      margin-right: 40px;
    }

    &__product {
      border-bottom: 1px solid #c9c9c9 !important;
    }

    &__product:nth-child(3n + 1):nth-last-child(1),
    &__product:nth-child(3n + 1):nth-last-child(2),
    &__product:nth-child(3n + 1):nth-last-child(3),
    &__product:nth-child(3n + 1):nth-last-child(1) ~ &__product,
    &__product:nth-child(3n + 1):nth-last-child(2) ~ &__product,
    &__product:nth-child(3n + 1):nth-last-child(3) ~ &__product {
      border-bottom: none !important;
    }
  }

  @media (max-width: $l) {
    &__order {
      align-items: center;
      justify-content: flex-start;
    }

    &__section.is-color {
      width: 100%;
      max-width: 100%;
    }

    &__price {
      width: auto;
      margin-right: 80px;
    }
  }

  @media (max-width: $m) {
    &__section {
      margin-top: 30px;
    }

    &__group {
      padding-right: 20px;
      padding-left: 20px;
    }

    &__title {
      padding-left: 20px;
      padding-right: 20px;
    }

    &__group.is-order {
      margin: 20px 0;
    }

    &__list {
      margin: 0;
    }

    &__order {
      margin: 20px 0;
      justify-content: space-between;
    }

    &__button {
      min-width: auto !important;
    }

    &__price {
      margin: 0;
    }

    &__price-value {
      font-size: 22px;
    }
  }

  @media (max-width: $s) {
    &__item {
      flex-flow: row wrap;
      align-items: flex-start;

      &.is-current {
        font-weight: inherit;
      }

      &-title {
        flex: 1;
        line-height: 1.1;
      }

      &-price {
        flex-grow: 1;
        width: 100%;
        margin-top: 5px;
        margin-left: 45px;
        font-weight: 700;
      }
    }
  }
}

/**
