  position: relative;
  opacity: 0;
  z-index: 0;
  overflow: hidden;

  &.is-inited {
    opacity: 1;
  }

  &__arrows {
    width: 100%;
    text-align: center;
  }

  &__arrow {
    display: inline-block;
    width: 20px;
    height: 20px;
    margin: 0 30px;
    padding: 0;
    position: relative;
    background-color: transparent;
    border: none;
    color: transparent;
    opacity: 0.6;
    cursor: pointer;
    transition: ease opacity 400ms;

    &::after {
      content: '';
      display: block;
      width: 100%;
      height: 100%;
      position: absolute;
      left: 0;
      top: 0;
      background-image: url('/img/arrow.svg');
      background-repeat: no-repeat;
      background-position: 50% 50%;
      background-size: contain;
    }

    &.is-prev {
    }

    &.is-next {
      transform: rotate(180deg);
    }

    &:hover,
    &:focus {
      opacity: 1;
    }
    &:active {
      opacity: 1;
    }
  }

  &__inner {
    display: flex;
    justify-content: space-between;
    align-items: center;
    flex-flow: row wrap;
    height: 100%;
    position: relative;
  }

  &__content {
    width: 100%;
    height: 100%;
    max-width: 420px;
    flex-grow: 1;
    position: relative;
    z-index: 0;

    &::before {
      content: '';
      display: block;
      width: 249px;
      height: 385px;
      position: absolute;
      left: -115px;
      top: -90px;
      z-index: -1;
      background-image: url('/img/slider-decor2.png');
      background-repeat: no-repeat;
      background-position: 50% 50%;
      background-size: contain;
    }
  }

  &__title {
    margin: 0;
    color: #3a3e42;
    font-size: 42px;
    line-height: 1;
  }

  &__text {
    margin-top: 25px;
    color: #656d75;
    font-size: 16px;
    line-height: 27px;
  }

  &__mask {
    height: 100%;
    position: relative;
    z-index: 0;

    &::before,
    &::after {
      content: '';
      display: block;
      width: 113px;
      height: 108px;
      position: absolute;
      left: -90px;
      top: 70px;
      z-index: -1;
      background-image: url('/img/slider-decor5.png');
      background-repeat: no-repeat;
      background-position: 50% 50%;
      background-size: contain;
    }

    &:after {
      width: 83px;
      height: 60px;
      top: auto;
      bottom: 150px;
      background-image: url('/img/slider-decor1.png');
    }
  }

  &__slides {
    width: 534px;
    height: 534px;
    position: relative;
    z-index: 0;
    background-image: url('/img/slider-mask.png');
    background-repeat: no-repeat;
    background-position: 0 0;
    // background-size: contain;

    &::before,
    &::after {
      content: '';
      display: block;
      width: 109px;
      height: 106px;
      position: absolute;
      right: 40px;
      top: -10px;
      z-index: -1;
      background-image: url('/img/slider-decor3.png');
      background-repeat: no-repeat;
      background-position: 50% 50%;
      background-size: contain;
    }

    &:after {
      width: 189px;
      height: 174px;
      top: auto;
      bottom: 10px;
      right: -40px;
      background-image: url('/img/slider-decor4_.png');
    }
  }

  &__slide {
  }

  .slick-list {
    overflow: visible;
  }
}

/**
* social
*/

.social {
  $social: &;

  &__title {
    margin-bottom: 40px;
    padding-bottom: 40px;
    color: #333;
    position: relative;
    font-weight: 600;
    font-style: normal;
    font-size: 24px;
    text-align: center;

    &::after {
      content: '';
      display: block;
      width: 40px;
      height: 10px;
      position: absolute;
      left: calc(50% - 20px);
      bottom: 0;
      background-color: $accent-second;
    }
  }

  &__list {
    display: flex;
    justify-content: center;
    align-items: center;
  }

  &__item {
    display: block;
    width: 52px;
    height: 52px;
    color: #c1c1c1;
    background-color: transparent;
    cursor: pointer;
    transition: ease color 300ms;

    & + & {
      margin-left: 15px;
    }

    &:hover,
    &:focus {
      color: #f8b248;
    }
    &:active {
      color: #f8b248 - 20;
    }
  }

  &__icon {
    width: 100%;
    height: 100%;
    color: currentColor;
  }

  @media (max-width: $md) {
    &__title {
      margin-bottom: 30px;
      padding-bottom: 30px;
    }

    &__item {
      width: 44px;
      height: 44px;

      & + & {
        margin-left: 12px;
      }
    }
  }

  @media (max-width: $s) {
    &__title {
      font-size: 21px;
    }
  }
}

/**
* sort
*/

.sort {
  $sort: &;

  padding: 0 80px;
  min-height: 58px;
  background-color: #fff;

  &__inner {
    display: flex;
    align-items: baseline;
    justify-content: space-between;
  }

  &__label {
    color: #333;
    font-weight: 400;
    font-size: 13px;
  }

  &__list {
    flex-grow: 1;
    margin-left: 10px;
  }

  &__item {
    position: relative;
    padding: 20px 15px;
    z-index: 0;
    color: #333;
    background-color: transparent;
    border: 0;
    font-family: $font-family;
    font-weight: 600;
    font-style: normal;
    font-size: 13px;
    cursor: pointer;
    outline: none;
    transition: ease color 300ms;

    svg {
      width: 8px;
      height: 8px;
      margin-left: 2px;
      position: relative;
      top: 1px;
      color: currentColor;
      transition: ease transform 300ms;
    }

    &.is-desc svg {
      top: 0px;
      transform: rotate(180deg);
    }

    &:hover,
    &:focus,
    &.is-active {
      color: #246eb4;
    }

    &:active {
      color: #246eb4 - 20;
    }
  }

  @media (max-width: $xl) {
    padding: 0 40px;
  }

  @media (max-width: $s) {
    min-height: 54px;
    padding: 0 20px;

    &__label {
      font-size: 11px;
    }

    &__item {
      padding: 18px 5px;
    }
  }
}

/**
* tabs
*/

.tabs {
  $tabs: &;

  margin: 0;
  position: relative;
  z-index: $layer-1;

  &__header {
    position: relative;
    margin: 0;
  }

  &__label {
    display: block;
    margin: 0;
    padding: 10px 0;
    border: none;
    color: #333;
    border-bottom: 4px solid #c9c9c9;
    font-weight: 700;
    font-style: normal;
    font-size: 18px;
    text-align: center;
    text-decoration: none;
    line-height: 1;
    cursor: pointer;
    transition: ease color 300ms, ease border 300ms;

    & + & {
      margin-left: 70px;
    }

    &:hover,
    &:focus {
      border-color: #e88e09;
      color: #e88e09;
    }

    &.is-active {
      pointer-events: none;
      border-color: #e88e09;
      color: #e88e09;
    }
  }

  &__body {
    margin-top: 40px;
  }

  &__inner {
  }

  &__item {
    &.is-active {
    }
  }

  @media (max-width: $l) {
    &__body {
      margin-top: 30px;
    }
  }

  @media (max-width: $m) {
    &__label {
      font-size: 16px;

      & + & {
        margin-left: 40px;
      }
    }
  }
}

/**
* text
*/

.text {
  $text: &;

  p {
    margin: 25px 0;

    &:last-child:first-child {
      margin: 0;
    }

    &:first-child {
      margin-top: 0;
    }

    &:last-child {
      margin-bottom: 0;
    }
  }
}

/**
* totop
*/

.totop {
  $totop: &;

  display: none;
  padding: 0;
  padding-right: 45px;
  position: relative;
  border: none;
  background-color: transparent;
  border-radius: 50%;
  color: #afb7ca;
  outline: none;
  font-family: $font-family;
  font-weight: 500;
  font-size: 12px;
  line-height: 1;
  letter-spacing: 0.16em;
  text-transform: uppercase;
  transition: color ease 400ms;
  cursor: pointer;

  &::before {
    content: '';
    display: block;
    width: 36px;
    height: 36px;
    position: absolute;
    right: 0;
    top: calc(50% - 18px);
    background-color: #ffffff;
    border: 1px solid currentColor;
    border-radius: 50%;
    transition: border-color ease 400ms;
  }

  &::after {
    content: '';
    display: block;
    width: 12px;
    height: 10px;
    top: calc(50% - 6px);
    right: 12px;
    position: absolute;
    background-image: url('/img/arrow.svg');
    background-repeat: no-repeat;
    background-position: 50% 50%;
    background-size: contain;
    transform: rotate(90deg);
  }

  &:hover,
  &:focus {
    color: $accent;

    &::before {
      border-color: $accent;
    }
  }

  &:active {
    color: $accent - 20;

    &::before {
      border-color: $accent - 20;
    }
  }
}
