/* Grid
----------------------------- */
.container {
  position: relative;
  width: 100%;
  min-width: $minwidth;
  max-width: 1410px;
  height: inherit;
  margin-left: auto;
  margin-right: auto;
  padding-left: 0;
  padding-right: 0;
  // padding-right: $gap + px;
  // padding-left: $gap + px;

  &.is-wide {
    max-width: $maxwidth;
    padding-left: 120px;
    padding-right: 120px;
  }

  &.is-narrow {
    max-width: 1150px;
  }
}

.grid {
  display: flex;
  flex-flow: row wrap;
  justify-content: flex-start;

  &.is-gap {
    margin: 0 $gap / -2 + px;

    @media (max-width: $m) {
      margin: -7px;
    }
  }

  &.is-collapse {
    margin: -10px;
  }

  &.is-column {
    flex-direction: column;
  }

  &.is-fit {
    justify-content: space-between;
  }

  &.is-around {
    justify-content: space-around;
  }

  &.is-end {
    justify-content: flex-end;
  }

  &.is-center {
    justify-content: center;
  }

  &.is-nowrap {
    flex-flow: row nowrap;
  }

  &.is-v-center {
    align-items: center;
  }

  &.is-v-top {
    align-items: flex-start;
  }

  &.is-v-bottom {
    align-items: flex-end;
  }

  &__item {
    display: block;
    float: left;
    flex: 0 0 auto;

    &.is-grow {
      flex-grow: 1;
    }

    &.is-shrink {
      flex-shrink: 1;
    }
  }
}

.order-0 {
  order: -1;
}
.order-1 {
  order: 1;
}
.order-2 {
  order: 2;
}
.order-3 {
